import axios from "axios";
import endPoints from "@/endPoints";
import browserStorage from "./utilities/helpers/storage";

// Create a new axios instance
const axiosInstance = axios.create({
  baseURL: endPoints.base_url,
  headers: {
    "Content-Type": "application/json",
  },
});

// Request interceptor to add the token to headers
axiosInstance.interceptors.request.use(
  (config) => {
    const token = browserStorage.get("auth_token"); // Get the token from Vuex state
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  },
);

// Response interceptor to handle errors (e.g., token expiration)
axiosInstance.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    console.error("error", error);
    if (error?.response?.status === 401) {
      browserStorage.remove("auth_token");
      // router.go(); // Redirect to login page
    }
    return Promise.reject(error);
  },
);

export default axiosInstance;
