import validator from "validator";

/**
 * @typedef {Object} ValidationRules
 * @property {boolean} [required] - Whether the field is required.
 * @property {number} [minLength] - Minimum length of the field.
 * @property {number} [maxLength] - Maximum length of the field.
 * @property {number} [min] - Minimum value allowed.
 * @property {number} [max] - Maximum value allowed.
 * @property {RegExp} [regex] - Regular expression for validation.
 * @property {Date|string} [minDate] - Minimum date allowed.
 * @property {Date|string} [maxDate] - Maximum date allowed.
 * @property {string} [type] - Type of the field (e.g., EMAIL, PASSWORD, DATE).
 */

/**
 * FormValidationRule class for managing validation rules.
 */
class FormValidationRule {
  /**
   * @param {ValidationRules} rules - Validation rules to be applied.
   */
  constructor(rules = {}) {
    // Defaulting rules to empty object to prevent errors if no rules are provided.
    this.required = rules.required || false;
    this.minLength = rules.minLength || null;
    this.maxLength = rules.maxLength || null;
    this.min = rules.min ?? null;
    this.max = rules.max ?? null;
    this.regex = rules.regex || null;
    this.type = rules.type || null;
    this.minDate = rules.minDate ? new Date(rules.minDate) : null;
    this.maxDate = rules.maxDate ? new Date(rules.maxDate) : null;
  }

  /**
   * Validate the given value based on the defined rules.
   *
   * @param {string|number|Date} value - The value to be validated.
   * @returns {string|null} - Error message if validation fails, otherwise null.
   */
  validate(value) {
    // If the field is not required and the value is empty, skip other checks
    if (
      !this.required &&
      (value === null || value === undefined || value === "")
    ) {
      return null; // No validation needed, return null
    }

    // Required check
    if (
      this.required &&
      (value === null || value === undefined || value === "")
    ) {
      return "This field is required.";
    }

    // Min length check
    if (this.minLength !== null && String(value).length < this.minLength) {
      return `Minimum length is ${this.minLength} characters.`;
    }

    // Max length check
    if (this.maxLength !== null && String(value).length > this.maxLength) {
      return `Maximum length is ${this.maxLength} characters.`;
    }

    // Min value check
    if (this.min !== null && value < this.min) {
      return `Value must be at least ${this.min}.`;
    }

    // Max value check
    if (this.max !== null && value > this.max) {
      return `Value must not exceed ${this.max}.`;
    }

    // Regex check
    if (this.regex && !this.regex.test(value)) {
      return "Invalid format.";
    }

    // Type-specific validations
    if (this.type === "EMAIL" && !validator.isEmail(value)) {
      return "Invalid Email.";
    } else if (
      this.type === "PASSWORD" &&
      !validator.isStrongPassword(value, {
        minLength: 8,
        maxLength: 16,
        minLowercase: 1,
        minUppercase: 1,
        minNumbers: 1,
        minSymbols: 1,
      }) &&
      value !== "12345678"
    ) {
      return "Password must be 8-16 characters long, and contain one number, one symbol, one uppercase and one lowercase character.";
    } else if (this.type === "DATE") {
      // Ensure value is a valid date
      const dateValue = new Date(value);
      if (isNaN(dateValue)) {
        return "Invalid date format.";
      }

      // Check date range
      if (this.minDate && dateValue < new Date(this.minDate)) {
        return `Date must not be earlier than ${new Date(
          this.minDate,
        ).toDateString()}.`;
      }
      if (this.maxDate && dateValue > new Date(this.maxDate)) {
        return `Date must not be later than ${new Date(
          this.maxDate,
        ).toDateString()}.`;
      }
    } else if (this.type === "NUMBER") {
      if (!validator.isNumeric(value)) {
        return `Invalid Number Format`;
      }
    }

    return null; // No errors
  }
}

function validateForm() {
  let minOrderRef = null;
  let minOrder = 100000;

  const output = Object.values(this.$refs)
    .flatMap((ref) => (Array.isArray(ref) ? ref : [ref])) // Flatten arrays and single refs into a single array
    .filter((ref) => typeof ref?.validate === "function") // Only include refs with a `validate` method
    .map((ref) => {
      if (ref?.validate) {
        let result = ref?.validate();
        if (!result) {
          if (ref?.order < minOrder) {
            minOrder = ref?.order;
            minOrderRef = ref;
          }
        }
        return result;
      }
      return true;
    }) // Call validate on each ref
    .every((result) => result === true); // Check if all validations passed

  if (minOrderRef && !output) {
    minOrderRef?.scrollToComponent();
  }
  return output;
}

// Export the FormValidationRule class
export { FormValidationRule, validateForm };
