export const computeProductTableData = (data) => {
  // const { max: maxPrice, min: minPrice } = getMinAndMaxFinalPrice(
  //   data,
  //   coefficient
  // );
  let finalData = data?.map((item) => {
    return {
      companyId: item?._id,
      companyName: item?.companyName,
      productVariable: item?.product?.variable,
      productPrice: !isNaN(item?.product?.price)
        ? item?.product?.price
        : "To be added",
      finalPrice: !isNaN(item?.product?.price)
        ? item?.product?.price
        : "Not Available",
      sfc: !isNaN(item?.product?.price) ? "NA" : "Not Available",
      companyGS: item?.GS,
      addedOn: item.product?.addedOn,
      variable: item?.quotation?.variableId,
    };
  });
  return finalData;
};

export const wait = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

export const buildErrorMessage = (error) => {
  return (
    error?.response?.data?.message ||
    error?.message ||
    error?.customMessage ||
    "An unknown error occurred"
  );
};

/**
 * Get the date of `x` days before or after today.
 *
 * @param {number} days - Number of days to add (positive) or subtract (negative).
 * @returns {string} - The resulting date in `YYYY-MM-DD` format.
 */
export const getDateFromToday = (days) => {
  const today = new Date();
  const resultDate = new Date(today);
  resultDate.setDate(today.getDate() + days);
  return resultDate.toISOString().split("T")[0]; // Format as YYYY-MM-DD
};

/**
 * Get the next date based on provided interval and weekend allowance.
 *
 * @param {string|Date} date - The starting date to which the interval will be added. Can be a string (e.g., "2024-11-25") or a Date object.
 * @param {number} interval - The number of days to add to the provided date.
 * @param {boolean} weekendAllowed - If true, weekends (Saturday and Sunday) are included in the calculation.
 *                                   If false, weekends are skipped.
 * @returns {Date} The resulting date after the interval and weekend adjustment.
 */
export const getNextDate = (date, interval, weekendAllowed) => {
  const currentDate = new Date(date);
  if (isNaN(currentDate)) {
    throw new Error("Invalid date provided");
  }

  // Add the interval to the date
  currentDate.setDate(currentDate.getDate() + interval);

  // Adjust if weekends are not allowed
  let shifted = 0;
  if (!weekendAllowed) {
    const dayOfWeek = currentDate.getDay();
    if (dayOfWeek === 6) {
      // Saturday
      currentDate.setDate(currentDate.getDate() + 2);
      shifted = 2;
    } else if (dayOfWeek === 0) {
      // Sunday
      currentDate.setDate(currentDate.getDate() + 1);
      shifted = 1;
    }
  }

  return { date: currentDate?.toISOString()?.substring(0, 10), shifted };
};
