<template>
  <div class="flex h-full w-full overflow-y-auto bg-bg-neutral-default">
    <div
      class="h-full w-full overflow-auto px-spacing-200 py-spacing-300 ipad:px-spacing-300"
      :class="selectedProduct ? 'lg:w-[30%]' : ''"
    >
      <div
        class="mb-spacing-300 flex items-center"
        :class="
          selectedProduct
            ? 'flex-col gap-spacing-200'
            : 'flex-col gap-spacing-300 ipadPro:flex-row'
        "
      >
        <div class="flex items-center gap-spacing-150">
          <h1
            class="text-overline/l/relaxed/500 text-content-on-neutral-secondary ipad:text-overline/xl/relaxed/500"
          >
            CATEGORIES
          </h1>
          <div
            @click="addChild()"
            class="flex h-spacing-250 w-spacing-250 cursor-pointer items-center justify-center rounded-full bg-constant-brand-primary"
          >
            <IconPlus
              class="h-spacing-200 w-spacing-200 text-constant-neutral-white"
            />
          </div>
        </div>
        <div class="flex items-center gap-spacing-150">
          <GeneralButton
            :button-type="'button'"
            :type="'success'"
            :size="'small'"
            :emphasis="'secondary'"
            :label="'Refresh'"
            :icon-slot="true"
            :icon-position="'leading'"
            @click.native="getCategoryList()"
          >
            <template #leading-icon>
              <i
                class="fa-solid fa-refresh relative bottom-spacing-50"
                :class="isLoading ? 'animate-spin' : ''"
              ></i>
            </template>
          </GeneralButton>
          <GeneralButton
            :button-type="'button'"
            :type="'brand'"
            :size="'small'"
            :emphasis="'secondary'"
            :label="'Expand'"
            :icon-slot="true"
            :icon-position="'leading'"
            @click.native="expandTreeView()"
          >
            <template #leading-icon>
              <i class="fa-solid fa-expand relative bottom-spacing-50"></i>
            </template>
          </GeneralButton>

          <GeneralButton
            :button-type="'button'"
            :type="'brand-secondary'"
            :size="'small'"
            :emphasis="'secondary'"
            :label="'Collapse'"
            :icon-slot="true"
            :icon-position="'leading'"
            @click.native="collapseTreeView()"
            ><template #leading-icon>
              <i class="fa-solid fa-compress relative bottom-spacing-50"></i>
            </template>
          </GeneralButton>
        </div>
        <Search
          placeholder="Search..."
          @search="searchByItemName"
          label="Name"
          search-id="commercial-item-name-search"
          class="ipadPro:max-w-[400px]"
        />
      </div>
      <draggable
        :list="data"
        @start="onDragStart"
        @end="onDragEnd"
        :disabled="!canDrag"
        ghost-class="opacity-40"
        group="tree"
        :id="'root'"
      >
        <div v-for="(child, index) in data" :key="child?._id">
          <TreeView
            class="mb-spacing-150"
            :node-obj="child"
            :level="Number(0)"
            :index="index"
            :expanded-tree-view="expandedTreeView"
            :selected-product="selectedProduct"
            :search-item-name="searchItemName"
          />
        </div>
      </draggable>
    </div>
    <div
      class="hidden h-full bg-bg-neutral-raised pt-spacing-300 lg:block lg:w-[70%]"
      v-if="selectedProduct?._id"
    >
      <ProductDetailsTable />
    </div>
    <ProductDetailsTableModal class="lg:hidden" />
    <AddCategoryModal />
  </div>
</template>
<script>
import { mapActions } from "vuex";
import draggable from "vuedraggable";
export default {
  components: {
    draggable,
    TreeView: () => import("@/components/Category/TreeView.vue"),
    ProductDetailsTable: () => import("@/components/ProductDetails/Table.vue"),
    AddCategoryModal: () =>
      import("@/components/Modals/AddNewCommercialItem.vue"),
    IconPlus: () => import("@/components/svgIcons/plus.vue"),
    ProductDetailsTableModal: () =>
      import("@/components/Modals/ProductDetailsTableModal.vue"),
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
    Search: () => import("@/components/General/Search.vue"),
  },
  data() {
    return {
      data: [],
      expandedTreeView: { expand: false },
      selectedProduct: null,
      isDragging: false,
      canDrag: true,
      clonedData: null,
      selectProductListener: null,
      handleStateListener: null,
      refetchListener: null,
      isLoading: false,
      searchItemName: "",
    };
  },
  mounted() {
    this.handleStateListener = (data) => {
      if (data?.type === "CLONE") {
        this.clonedData = structuredClone(this.data);
      } else if (data?.type === "RESTORE") {
        this.data = structuredClone(this.clonedData);
      }
    };

    this.refetchListener = () => {
      this.getCategoryList();
    };

    this.selectProductListener = (node) => {
      this.selectedProduct = node;
      if (node) {
        this.$bus.$emit("product-details-table-modal", {
          showModal: true,
          productId: node?._id,
        });
      }
    };

    this.$bus.$on("handle-commercial-items-state", this.handleStateListener);
    this.$bus.$on("refetch-commercial-items", this.refetchListener);
    this.$bus.$on("set-selected-product", this.selectProductListener);
    this.getCategoryList();
  },

  methods: {
    ...mapActions("category", ["getCommercialItems", "reorderCommercialItems"]),
    async getCategoryList() {
      this.isLoading = true;
      this.$bus.$emit("showLoader", {
        showLoader: true,
      });
      const response = await this.getCommercialItems({
        params: { mode: "FULL" },
      });
      if (response?.success) {
        this.data = response?.data;
      } else {
        this.$toast.error("Unable to load data");
      }
      this.$bus.$emit("showLoader", false);
      this.isLoading = false;
    },
    addChild() {
      this.$bus.$emit("add-new-commercial-item-modal", {
        showModal: true,
        parent: null,
      });
    },
    async expandTreeView() {
      this.expandedTreeView = { expand: true };
    },
    async collapseTreeView() {
      this.expandedTreeView = { expand: false };
    },
    onDragStart() {
      this.clonedData = structuredClone(this.data);
      this.isDragging = true;
    },
    onDragEnd(e) {
      this.isDragging = false;
      if (e?.from?.id === e?.to?.id && e.oldIndex === e.newIndex) return;
      this.reorderChildNodes(e?.from?.id, e?.to?.id, e?.oldIndex, e?.newIndex);
    },
    async reorderChildNodes(from, to, oldIndex, newIndex) {
      this.canDrag = false;
      this.$bus.$emit("showLoader", { showLoader: true });
      const res = await this.reorderCommercialItems({
        body: {
          from,
          to,
          oldIndex,
          newIndex,
          itemId: this.clonedData?.[oldIndex]?._id,
        },
      });
      if (res?.success) {
        this.$bus.$emit("showLoader", false);
        this.$toast.success(res?.message);
        this.$bus.$emit("refetch-commercial-items");
      } else {
        this.$bus.$emit("showLoader", false);
        this.$toast.error(res?.message);
        this.data = structuredClone(this.clonedData);
      }
      this.clonedData = null;
      this.canDrag = true;
    },
    searchByItemName(query) {
      this.searchItemName = query;
      this.expandedTreeView = { expand: true };
    },
  },
  beforeDestroy() {
    this.$bus.$off("set-selected-product", this.selectProductListener);
    this.$bus.$off("refetch-commercial-items", this.refetchListener);
    this.$bus.$off("handle-commercial-items-state", this.handleStateListener);
  },
};
</script>
