<template>
  <div class="flex h-full w-full flex-col bg-bg-neutral-default pt-spacing-300">
    <div
      class="flex w-full items-center gap-spacing-300 px-spacing-200 ipad:px-spacing-300"
    >
      <h1
        class="text-overline/l/relaxed/500 text-content-on-neutral-secondary ipad:text-overline/xl/relaxed/500"
      >
        MARKET RESEARCHES
      </h1>
      <div class="flex items-center gap-spacing-200">
        <GeneralButton
          :button-type="'button'"
          :type="'brand'"
          :size="'small'"
          :emphasis="'secondary'"
          :icon-slot="true"
          :icon-position="'leading'"
          :is-disabled="false"
          :is-loading="false"
          :label="'New'"
          @click.native="handleClickNew()"
        >
          <template #leading-icon>
            <i class="fa-solid fa-plus relative bottom-spacing-50"></i>
          </template>
        </GeneralButton>
        <GeneralButton
          :button-type="'button'"
          :type="'success'"
          :size="'small'"
          :emphasis="'secondary'"
          :label="'Refresh'"
          :icon-slot="true"
          :icon-position="'leading'"
          @click.native="fetchDebouncedMarketResearches()"
        >
          <template #leading-icon>
            <i
              class="fa-solid fa-refresh relative bottom-spacing-50"
              :class="isLoading ? 'animate-spin' : ''"
            ></i>
          </template>
        </GeneralButton>
      </div>
    </div>
    <div
      class="mt-spacing-200 w-full max-w-[1000px] px-spacing-200 ipad:px-spacing-300"
    >
      <ProductSearchDropdown
        :dropdown-label="'Products'"
        :dropdown-id="'products'"
        :products="products"
        :is-multi-select="true"
        @on-products-update="updateProducts"
        :show-error="false"
        :show-selected-list="true"
      />
    </div>
    <div
      class="flex w-full max-w-[1000px] flex-col items-center gap-spacing-200 px-spacing-200 pb-spacing-100 pt-spacing-250 ipad:flex-row ipad:px-spacing-300"
    >
      <Search
        placeholder="Search..."
        @search="seachByResearchName"
        label="Market Research"
        search-id="market-research-name-search"
      />

      <GeneralSelect
        :label="'Tag'"
        :options="tagDropDownData"
        :selected-option-key="tagFilter"
        @option-selected="handleSelectedTag"
        :show-error="false"
      />
    </div>

    <!-- Using the Paginated Table Component -->
    <PaginatedTable
      :headers="headers"
      :current-page="currentPage"
      :total-pages="totalPages"
      :sort-key="sortKey"
      :sort-order="sortOrder"
      :error="error"
      :is-loading="isLoading"
      @fetch-sorted-data="handleSort"
      @fetch-data="fetchMarketResearches"
    >
      <template #table-body>
        <tr
          v-for="item in getMappedData"
          :key="item._id"
          class="cursor-pointer text-body/m/relaxed/400 text-content-on-neutral-primary ipad:text-body/l/relaxed/400"
          @click="goToDetailsPage(item?._id)"
        >
          <td
            class="gap-spacing-50 py-spacing-200 pr-spacing-50 ipad:pr-spacing-100"
          >
            <span
              class="text-body/m/relaxed/500 text-content-on-neutral-primary ipad:text-body/l/relaxed/500"
            >
              {{ item?.leadRepresentative?.name }}<br />
            </span>
            <span
              v-for="user in item?.group"
              :key="user?._id"
              class="text-body/m/relaxed/400 text-content-on-neutral-secondary ipad:text-body/l/relaxed/400"
              >{{ user?.name }}<br
            /></span>
          </td>
          <td class="py-spacing-200 pr-spacing-50 ipad:pr-spacing-100">
            {{ item.dateStart | toDate }}
          </td>
          <td
            class="py-spacing-200 pr-spacing-50 text-body/m/relaxed/500 ipad:pr-spacing-100 ipad:text-body/l/relaxed/500"
          >
            <p v-html="$highlightMatch(item.name, search)" />
          </td>
          <td class="py-spacing-200 pr-spacing-50 ipad:pr-spacing-100">
            {{ item.percentage | toPercentage }}
          </td>
          <td class="py-spacing-200 pr-spacing-50 ipad:pr-spacing-100">
            <GeneralTag
              :type="getTagType(item?.tag?.key)"
              :content="item?.tag?.value"
              class="whitespace-nowrap"
            />
          </td>
          <td
            class="py-spacing-200 pr-spacing-50 text-content-on-neutral-tertiary ipad:pr-spacing-100"
          >
            <span
              v-for="(product, index) in item?.products"
              :key="index"
              class="text-body/m/relaxed/400 text-content-on-neutral-primary ipad:text-body/l/relaxed/400"
              >{{ index + 1 }}. {{ product?.title }}<br
            /></span>
          </td>
          <td
            class="py-spacing-200 pr-spacing-50 text-content-on-neutral-tertiary ipad:pr-spacing-100"
          >
            {{ item.note || "-----------" }}
          </td>
        </tr>
        <tr
          v-show="isLoading"
          v-for="(_, row) in new Array(6).fill('')"
          :key="`dummy-row-${row}`"
          class="text-left"
        >
          <td
            v-for="(_, col) in new Array(7).fill('')"
            :key="`dummy-col-${col}`"
            class="gap-spacing-25 py-spacing-150 pr-spacing-100"
          >
            <GeneralSkeleton
              :is-table-cell="true"
              :class="
                col === 3
                  ? 'h-spacing-300 w-spacing-400'
                  : 'h-spacing-300 w-spacing-1200'
              "
            />
          </td>
        </tr>
      </template>
    </PaginatedTable>
    <AddNewMarketResearchModal />
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from "vuex";
import { debounce } from "lodash";
export default {
  components: {
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
    Search: () => import("@/components/General/Search.vue"),
    PaginatedTable: () =>
      import("@/components/General/Table/PaginatedTable.vue"),
    GeneralTag: () => import("@/components/General/Tag.vue"),
    GeneralSelect: () => import("@/components/General/Select.vue"),
    AddNewMarketResearchModal: () =>
      import("@/components/Modals/AddNewMarketResearch.vue"),
    GeneralSkeleton: () => import("@/components/General/Skeleton.vue"),
    ProductSearchDropdown: () =>
      import("@/components/Utility/CustomDropdowns/ProductSearch.vue"),
  },
  data() {
    return {
      headers: [
        { key: "group", value: "Group", sortable: false },
        { key: "dateStart", value: "Date Start", sortable: true },
        { key: "name", value: "Name", sortable: true },
        { key: "percentage", value: "%", sortable: true },
        { key: "tag", value: "Tag", sortable: false },
        { key: "prodcuts", value: "Products", sortable: false },
        { key: "note", value: "Note", sortable: false },
      ],
      sortKey: "dateStart",
      sortOrder: "desc",
      search: "",
      tagFilter: "", // Selected tag for exact matching
      limit: 10,
      tagDropDownData: [
        { key: "", value: "Select Tag", default: true },
        { key: "IN_PROGRESS", value: "in progress" },
        { key: "PENDING_APPROVAL", value: "pending approval" },
        { key: "COMPLETED", value: "completed" },
        { key: "STARTING_SOON", value: "starting soon" },
      ],
      products: [],
      fetchDebouncedMarketResearches: debounce(this.fetchMarketResearches, 300),
      isLoading: false,
      currentPage: 0,
    };
  },
  mounted() {
    if (this.marketResearchesData) {
      this.RESET_MARKET_RESEARCHES_DATA();
    }
    this.$bus.$on("refetch-market-researches", () => {
      this.fetchDebouncedMarketResearches();
    });
    this.fetchDebouncedMarketResearches();
  },
  computed: {
    ...mapState({
      marketResearchesData: (state) =>
        state.marketResearch.marketResearchesData,
      marketResearches: (state) =>
        state.marketResearch.marketResearchesData?.data,
      currentPageNumber: (state) =>
        state.marketResearch.marketResearchesData?.pagination?.currentPage || 0,
      totalPages: (state) =>
        state.marketResearch.marketResearchesData?.pagination?.totalPages || 1,
      totalCount: (state) =>
        state.marketResearch.marketResearchesData?.pagination?.total || 0,
      error: (state) => state.marketResearch.error,
    }),
    getProductIds() {
      let output = [];
      output = this.products?.map((item) => item?._id);
      return output;
    },
    getMappedData() {
      // Create a deep copy of `marketResearches` to avoid altering store data
      const output = this.marketResearches?.map((item) => {
        // Spread item to create a shallow copy of each item
        return {
          ...item,
          // Map `tag` without changing the original item in the store
          tag: this.tagDropDownData.find((data) => data?.key === item?.tag),
        };
      });

      return output;
    },
  },
  methods: {
    ...mapActions("marketResearch", ["getMarketResearches"]),
    ...mapMutations("marketResearch", ["RESET_MARKET_RESEARCHES_DATA"]),

    async fetchMarketResearches(page = 1, resolve) {
      if (this.isLoading) {
        return;
      }
      this.currentPage = page;
      this.isLoading = true; // Set loading to true right away

      // Ensure DOM has updated before fetching data
      await this.$nextTick();

      let payload = {
        params: {
          page,
          limit: this.limit,
          sortBy: this.sortKey,
          order: this.sortOrder,
          filter: {
            tag: this.tagFilter,
            search: this.search,
            products: this.getProductIds,
          },
        },
      };
      await this.getMarketResearches(payload);
      this.currentPage = this.currentPageNumber;
      this.isLoading = false; // Set loading to false when done
      if (resolve) {
        resolve();
      }
    },
    handleClickNew() {
      this.$bus.$emit("add-new-market-research-modal", { showModal: true });
    },
    handleSort({ sortKey, sortOrder }) {
      this.sortKey = sortKey;
      this.sortOrder = sortOrder;
      this.fetchDebouncedMarketResearches();
    },
    getTagType(tag) {
      let output = "";
      if (tag === "IN_PROGRESS") {
        output = "warning";
      } else if (tag === "PENDING_APPROVAL") {
        output = "error";
      } else if (tag === "COMPLETED") {
        output = "success";
      } else if (tag === "STARTING_SOON") {
        output = "brand";
      }
      return output;
    },
    handleSelectedTag(tag) {
      this.tagFilter = tag;
      this.fetchDebouncedMarketResearches();
    },
    goToDetailsPage(id) {
      this.$router.push({ path: `/dashboard/market-researches/${id}` });
    },
    seachByResearchName(query) {
      this.search = query;
      this.fetchDebouncedMarketResearches();
    },
    updateProducts(products) {
      this.products = products;
      this.fetchDebouncedMarketResearches();
    },
  },
  beforeDestroy() {
    this.$bus.$off("refetch-market-researches");
  },
};
</script>
