<template>
  <AuthWrapper :title="'Reset Password'" class="min-h-screen">
    <form
      @submit.prevent="resetPasswordFn"
      class="flex flex-1 flex-col overflow-y-auto px-spacing-300"
    >
      <div class="flex flex-1 flex-col gap-spacing-200 py-spacing-100">
        <div
          class="mb-spacing-100 flex items-start gap-spacing-100 rounded-08 border border-stroke-success bg-bg-status-success px-spacing-100 py-spacing-100 text-body/m/relaxed/400 text-content-on-status-on-success"
        >
          <div>
            <IconCheck
              class="relative top-spacing-25 h-spacing-200 w-spacing-200 text-current"
            />
          </div>
          <p>
            An OTP has been sent to
            <span class="text-body/m/relaxed/500">{{ email }}</span
            >. Please check your inbox (and spam folder) and enter the OTP.
          </p>
        </div>

        <FormInput
          :autofocus="true"
          v-model="otp"
          input-type="number"
          label="OTP"
          input-id="otp"
          ref="password-reset-otp"
          :field-validations="fieldValidations?.otp"
          @enter-pressed="$focusNextElement('new-password')"
          :min-length="6"
          :max-length="6"
        />
        <FormInput
          v-model="newPassword"
          input-type="password"
          ref="password-reset-new-password"
          label="New Password"
          input-id="new-password"
          :field-validations="fieldValidations?.newPassword"
          @enter-pressed="$focusNextElement('confirm-password')"
        />
        <FormInput
          v-model="confirmPassword"
          input-type="password"
          label="Confirm Password"
          ref="password-reset-confirm-password"
          input-id="confirm-password"
          :field-validations="fieldValidations?.confirmPassword"
          @enter-pressed="resetPasswordFn"
        />
      </div>
      <div class="sticky bottom-0 bg-bg-neutral-default pt-spacing-200">
        <GeneralButton
          :button-type="'button'"
          :type="'brand'"
          :size="'large'"
          :emphasis="'primary'"
          :icon-position="'none'"
          :icon="''"
          :image="''"
          :is-disabled="false"
          :is-loading="loading"
          :label="'Submit'"
          @click.native="resetPasswordFn"
        />
        <div class="mt-spacing-150 w-full">
          <p
            v-if="canResendOTP"
            class="text-center text-body/l/relaxed/400 text-content-on-neutral-secondary"
          >
            Didn't Recieve OTP?
            <button
              class="bg-transparent"
              @click.stop.prevent="handleOTPResend"
            >
              <span
                class="text-body/l/relaxed/500 hover:text-hover-constant-brand-primary"
                >Resend</span
              >
            </button>
          </p>
          <p
            v-else
            class="text-center text-body/l/relaxed/400 text-content-on-neutral-secondary"
          >
            Resend OTP in {{ timeLeft }} seconds.
          </p>
        </div>
      </div>
    </form>
  </AuthWrapper>
</template>

<script>
import { FormValidationRule } from "@/utilities/helpers/formValidations";
import { mapActions } from "vuex";
export default {
  components: {
    AuthWrapper: () => import("@/components/Auth/AuthWrapper.vue"),
    FormInput: () => import("@/components/General/FormInput.vue"),
    IconCheck: () => import("@/components/svgIcons/check.vue"),
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
  },
  props: {
    email: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      otp: "",
      newPassword: "",
      confirmPassword: "",
      fieldValidations: {
        otp: new FormValidationRule({
          minLength: 6,
          maxLength: 6,
          required: true,
        }),
        newPassword: new FormValidationRule({
          type: "PASSWORD",
          required: true,
        }),
        confirmPassword: new FormValidationRule({
          type: "PASSWORD",
          required: true,
        }),
      },
      canResendOTP: false,
      timeLeft: 60,
      resendInterval: null,
      loading: false,
    };
  },
  mounted() {
    if (!this.email) {
      this.$router.push("/login");
    } else {
      this.startTimer();
    }
  },
  methods: {
    ...mapActions({
      resetPassword: "auth/resetPassword",
      forgotPassword: "auth/forgotPassword",
    }),
    async resetPasswordFn() {
      const isValid = this.$validateForm();
      if (!isValid) {
        return;
      }
      if (this.newPassword !== this.confirmPassword) {
        this.$toast.error("Passwords do not match!");
        return;
      }
      this.loading = true;
      const res = await this.resetPassword({
        otp: this.otp,
        email: this.email,
        newPassword: this.newPassword,
      });
      if (res?.success) {
        this.$toast.success(res?.message);
        clearInterval(this.resendInterval);
        this.$router.push("/login");
      } else {
        this.$toast.error(res?.message);
      }
      this.loading = false;
    },
    async handleOTPResend() {
      this.$bus.$emit("showLoader", { showLoader: true });
      const res = await this.forgotPassword(this.email);
      if (res?.success) {
        this.startTimer();
        this.$toast.success(res?.message);
      } else {
        this.$toast.error(res?.message);
      }
      this.$bus.$emit("showLoader", false);
    },
    startTimer() {
      this.timeLeft = 10;
      this.canResendOTP = false;
      this.resendInterval = setInterval(() => {
        this.timeLeft--;
        if (this.timeLeft === 0) {
          this.clearTimer();
        }
      }, 1000);
    },
    clearTimer() {
      clearInterval(this.resendInterval);
      this.canResendOTP = true;
    },
  },
  beforeDestroy() {
    clearInterval(this.resendInterval);
    this.resendInterval = null;
  },
};
</script>
