<template>
  <AuthWrapper title="Login" class="min-h-screen">
    <form
      @submit.prevent="handleLogin"
      class="flex flex-1 flex-col overflow-y-auto px-spacing-300"
    >
      <div class="flex flex-1 flex-col gap-spacing-200 py-spacing-100">
        <FormInput
          ref="login-email"
          :autofocus="true"
          v-model="email"
          input-type="email"
          label="Email"
          input-id="email"
          autocomplete="on"
          :field-validations="fieldValidations.email"
          @enter-pressed="$focusNextElement('password')"
        />
        <FormInput
          ref="login-password"
          v-model="password"
          input-type="password"
          label="Password"
          :field-validations="fieldValidations.password"
          input-id="password"
          autocomplete="on"
          @enter-pressed="handleLogin"
        />

        <div class="flex w-full justify-start">
          <RouterLink
            to="/forgot-password"
            class="justify-end text-body/l/relaxed/400 text-constant-brand-primary"
          >
            Fogot password?
          </RouterLink>
        </div>
      </div>
      <div class="sticky bottom-0 bg-bg-neutral-default pt-spacing-200">
        <GeneralButton
          :button-type="'button'"
          :type="'brand'"
          :size="'large'"
          :emphasis="'primary'"
          :icon-position="'none'"
          :icon="''"
          :image="''"
          :is-disabled="false"
          :is-loading="isLoading"
          :label="'Login'"
          @click.native="handleLogin"
        />
        <div class="mt-spacing-150 w-full">
          <p
            class="text-center text-body/l/relaxed/400 text-content-on-neutral-secondary"
          >
            To Create an account, Contact Admin
            <!-- Don't have an account? -->
            <!-- <RouterLink
                  to="/signup"
                  class="hover:text-content-on-neutral-primary text-body/l/relaxed/500"
                >
                  Create Account</RouterLink
                > -->
          </p>
        </div>
      </div>
    </form>
  </AuthWrapper>
</template>

<script>
import { FormValidationRule } from "@/utilities/helpers/formValidations";
import { mapActions } from "vuex";
export default {
  components: {
    AuthWrapper: () => import("@/components/Auth/AuthWrapper.vue"),
    FormInput: () => import("@/components/General/FormInput.vue"),
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
  },
  data() {
    return {
      email: "",
      password: "",
      error: "",
      isLoading: false,
      fieldValidations: {
        email: new FormValidationRule({
          type: "EMAIL",
          required: true,
        }),
        password: new FormValidationRule({
          type: "PASSWORD",
          required: true,
        }),
      },
    };
  },
  mounted() {
    this.email = "";
    this.password = "";
  },
  methods: {
    ...mapActions("auth", ["login"]),

    async handleLogin() {
      const isValid = this.$validateForm();
      if (!isValid) {
        return;
      }
      this.isLoading = true;

      const res = await this.login({
        email: this.email,
        password: this.password,
      });
      if (res?.success) {
        this.$router.push(this.$route?.query?.redirect || "/dashboard");
      } else {
        this.$toast.error(res?.message);
      }
      this.isLoading = false;
    },
  },
};
</script>
