<template>
  <div class="h-full overflow-y-auto bg-bg-neutral-default px-spacing-300">
    <!-- Company Header -->
    <CompanyHeader v-if="!isLoading && companyData" :company="companyData" />
    <div v-else-if="isLoading" class="h-[60px] w-full"></div>

    <div
      class="flex w-full flex-col items-stretch gap-spacing-200 ipadPro:flex-row ipadPro:gap-spacing-600"
    >
      <!-- General Details -->
      <CompanyGeneralDetails :company="companyData" />

      <div class="w-full border border-stroke-bold ipadPro:w-0"></div>

      <!-- Origin Details -->
      <CompanyOriginDetails :company="companyData" />

      <div class="w-full border border-stroke-bold ipadPro:w-0"></div>
      <!-- Agent Details -->
      <CompanyAgentDetails :company="companyData" />
    </div>
    <div>
      <div class="mt-spacing-100">
        <!-- Categories -->
        <CompanyProducts :company="companyData" />
      </div>

      <div class="mt-spacing-400">
        <!-- Prices -->
        <CompanyPrices
          v-if="companyData"
          :prices="companyData?.prices"
          :company-id="companyData?._id"
        />
      </div>
    </div>
    <UpdateCategoryToCompanyModal />
    <ProductDetailsTableModal />
    <AddCategoryModal />
    <AddNewCompanyModal />
    <AddDetailsLaterModal />
    <CompanyMapModal />
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  components: {
    CompanyHeader: () =>
      import("@/components/CompanyDetails/CompanyHeader.vue"),
    CompanyGeneralDetails: () =>
      import("@/components/CompanyDetails/GeneralDetails.vue"),
    CompanyOriginDetails: () =>
      import("@/components/CompanyDetails/OriginDetails.vue"),
    CompanyAgentDetails: () =>
      import("@/components/CompanyDetails/AgentDetails.vue"),
    CompanyProducts: () =>
      import("@/components/CompanyDetails/CompanyProducts.vue"),
    CompanyPrices: () =>
      import("@/components/CompanyDetails/CompanyPrices.vue"),
    UpdateCategoryToCompanyModal: () =>
      import("@/components/Modals/UpdateCategoryToCompany.vue"),
    ProductDetailsTableModal: () =>
      import("@/components/Modals/ProductDetailsTableModal.vue"),
    AddCategoryModal: () =>
      import("@/components/Modals/AddNewCommercialItem.vue"),
    AddNewCompanyModal: () => import("@/components/Modals/AddNewCompany.vue"),
    AddDetailsLaterModal: () =>
      import("@/components/Modals/AddDetailsLaterModal.vue"),
    CompanyMapModal: () => import("@/components/Modals/CompanyMapModal.vue"),
  },
  data() {
    return {
      selectedDataMissingOption: "",
      isLoading: false,
    };
  },
  mounted() {
    const companyId = this.$route?.params?.id;

    this.fetchCompanyDetails(companyId);

    this.$bus.$on("refetch-company-details", () => {
      this.fetchCompanyDetails(companyId);
    });
  },
  computed: {
    ...mapState({
      companyData: (state) => state.company?.companyData,
      error: (state) => state.company?.error,
    }),
  },
  methods: {
    ...mapActions("company", ["getCompanyDetails"]),
    async fetchCompanyDetails(companyId) {
      this.isLoading = true;
      await this.getCompanyDetails({ id: companyId });
      if (this.error) {
        this.$router.push({ path: "/dashboard" });
      }
      this.isLoading = false;
    },
  },
  beforeDestroy() {
    this.$bus.$off("refetch-company-details");
  },
};
</script>
