<template>
  <div class="flex h-full w-full flex-col bg-bg-neutral-default lg:flex-row">
    <div
      class="flex w-full flex-1 flex-col overflow-y-auto"
      :class="showCreateForm ? 'lg:w-1/2' : ''"
    >
      <div
        class="flex w-full flex-col gap-spacing-300 px-spacing-200 pb-spacing-100 pt-spacing-300 ipad:px-spacing-300"
      >
        <div class="flex w-full items-center gap-spacing-300">
          <h1
            class="text-overline/l/relaxed/500 text-content-on-neutral-secondary ipad:text-overline/xl/relaxed/500"
          >
            MANAGE USERS
          </h1>
          <div class="flex items-center gap-spacing-200">
            <GeneralButton
              :button-type="'button'"
              :type="'brand'"
              :size="'small'"
              :emphasis="'secondary'"
              :icon-slot="true"
              :icon-position="'leading'"
              :is-disabled="false"
              :is-loading="false"
              :label="'New'"
              @click.native="handleClickNew()"
            >
              <template #leading-icon>
                <i class="fa-solid fa-plus relative bottom-spacing-50"></i>
              </template>
            </GeneralButton>
            <GeneralButton
              :button-type="'button'"
              :type="'success'"
              :size="'small'"
              :emphasis="'secondary'"
              :label="'Refresh'"
              :icon-slot="true"
              :icon-position="'leading'"
              @click.native="fetchDebouncedData()"
            >
              <template #leading-icon>
                <i
                  class="fa-solid fa-refresh relative bottom-spacing-50"
                  :class="isLoading ? 'animate-spin' : ''"
                ></i>
              </template>
            </GeneralButton>
          </div>
        </div>
        <div
          class="flex flex-col gap-spacing-200 nexus:flex-row ipad:gap-spacing-150"
        >
          <Search
            placeholder="Search..."
            @search="searchByUserName"
            label="Name"
            search-id="user-name-search"
          />
          <Search
            placeholder="Search..."
            @search="searchByUserEmail"
            label="Email"
            search-id="user-email-search"
          />
        </div>
      </div>
      <PaginatedTable
        :headers="headerData"
        :current-page="currentPage"
        :total-pages="totalPages"
        :sort-key="sortKey"
        :sort-order="sortOrder"
        :total-count="totalCount"
        :error="error"
        :is-loading="isLoading"
        @fetch-data="fetchData"
        @fetch-sorted-data="fetchSortedData"
      >
        <template #table-body>
          <tr
            v-for="item in users"
            :key="item._id"
            class="text-left text-body/m/relaxed/400 text-content-on-neutral-primary nexus:text-body/l/relaxed/400"
          >
            <td
              class="flex flex-col gap-spacing-25 border-b border-stroke-medium py-spacing-100 pr-spacing-100"
            >
              {{ item.createdAt | toDate }}
            </td>
            <td
              class="border-b border-stroke-medium py-spacing-100 pr-spacing-100"
            >
              <p v-html="$highlightMatch(item?.name, userName)" />
            </td>
            <td
              class="border-b border-stroke-medium py-spacing-100 pr-spacing-100"
            >
              <p v-html="$highlightMatch(item?.email, userEmail)" />
            </td>
            <td
              class="border-b border-stroke-medium py-spacing-100 pr-spacing-100"
            >
              {{ item?.trackers?.bulkAdds }}
            </td>
            <td
              class="border-b border-stroke-medium py-spacing-100 pr-spacing-100"
            >
              {{ item?.trackers?.newStarts }}
            </td>
            <td
              class="border-b border-stroke-medium py-spacing-100 pr-spacing-100"
            >
              {{ item?.trackers?.followUps }}
            </td>
            <td
              class="border-b border-stroke-medium py-spacing-100 pr-spacing-100"
            >
              {{ item?.role }}
            </td>
            <td
              class="border-b border-stroke-medium py-spacing-100 pr-spacing-100 text-content-on-neutral-secondary"
            >
              <div class="flex items-center gap-spacing-200">
                <IconEdit3
                  @click.native="handleEdit(item)"
                  class="h-spacing-200 w-spacing-200 cursor-pointer text-current hover:text-constant-status-info"
                />
                <IconTrash2
                  @click.native="handleDelte(item)"
                  class="h-spacing-200 w-spacing-200 cursor-pointer text-current hover:text-constant-status-error"
                />
              </div>
            </td>
          </tr>
          <tr
            v-show="isLoading"
            v-for="(_, row) in new Array(6).fill('')"
            :key="`dummy-row-${row}`"
            class="text-left"
          >
            <td
              v-for="(_, col) in new Array(8).fill('')"
              :key="`dummy-col-${col}`"
              class="gap-spacing-25 py-spacing-150 pr-spacing-100"
            >
              <GeneralSkeleton
                :is-table-cell="true"
                class="h-spacing-300 w-spacing-1200"
              />
            </td>
          </tr>
        </template>
      </PaginatedTable>
    </div>
    <div v-if="showCreateForm" class="nexus:min-w-[500px] lg:w-1/3">
      <SignupPage
        :formData="update ? selectedUserData : undefined"
        :update="update"
        :show-cross-icon="true"
        @close-pressed="handleClose"
      />
    </div>
  </div>
</template>

<script>
import { debounce } from "lodash";
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";

export default {
  components: {
    SignupPage: () => import("@/views/SignupPage.vue"),
    Search: () => import("@/components/General/Search.vue"),
    PaginatedTable: () =>
      import("@/components/General/Table/PaginatedTable.vue"),
    GeneralSkeleton: () => import("@/components/General/Skeleton.vue"),
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
    IconEdit3: () => import("@/components/svgIcons/edit-3.vue"),
    IconTrash2: () => import("@/components/svgIcons/trash-2.vue"),
  },
  data() {
    return {
      selectedUserData: null,
      update: false,
      showCreateForm: false,
      userName: "",
      userEmail: "",
      limit: 20,
      isLoading: false,
      sortKey: "createdAt",
      sortOrder: "desc",
      currentPage: 0,
      headerData: [
        { key: "createdAt", value: "Account Created", sortable: true },
        {
          key: "name",
          value: "Name",
          sortable: true,
          description: "User Name",
        },
        {
          key: "email",
          value: "Email",
          sortable: false,
          description: "User Email",
        },

        {
          key: "bulk-adds",
          value: "Bulk adds",
          sortable: false,
          description: "Number of companies bulk added by user",
        },
        {
          key: "new-starts",
          value: "New starts",
          sortable: false,
          description: "Number of new starts done by user",
        },
        {
          key: "follow-ups",
          value: "Follow ups",
          sortable: false,
          description: "Number of follow ups done by user",
        },
        {
          key: "role",
          value: "Role",
          sortable: false,
          description: "User Role Assigned",
        },
        {
          key: "action",
          value: "Action",
          sortable: false,
          description: "",
        },
      ],
      fetchDebouncedData: debounce(this.fetchData, 300),
      updateHandler: null,
    };
  },
  mounted() {
    if (this.user?.role !== "SUPER_ADMIN") {
      this.$router.push("/dashboard");
    }

    if (this.usersData) {
      this.RESET_USERS_DATA();
    }
    this.updateHandler = () => {
      this.fetchDebouncedData();
    };
    this.deleteHandler = async ({ id }) => {
      this.$bus.$emit("showLoader", { showLoader: true });
      const res = await this.deleteUserByID(id);
      if (res?.success) {
        this.handleClose();
        this.fetchDebouncedData();
        this.$toast.success(res?.message);
      } else {
        this.$toast.error(res?.message);
      }
      this.$bus.$emit("showLoader", false);
    };
    this.$bus.$on("refresh-users-list", this.updateHandler);
    this.$bus.$on("delete-user", this.deleteHandler);
  },
  computed: {
    ...mapState({
      usersData: (state) => state.auth?.usersData,
      users: (state) => state.auth?.usersData?.users,
      currentPageNumber: (state) => state.auth?.usersData?.currentPage || 0,
      totalPages: (state) => state.auth?.usersData?.totalPages || 1,
      totalCount: (state) => state.auth?.usersData?.totalCount || 0,
      error: (state) => state.auth?.error,
    }),
    ...mapGetters({
      user: "auth/getUser",
    }),
  },

  methods: {
    ...mapMutations("auth", ["RESET_USERS_DATA"]),
    ...mapActions("auth", ["getAllUsers", "deleteUserByID"]),
    async fetchData(page = 1, resolve) {
      if (this.isLoading) {
        return;
      }
      this.currentPage = page;
      this.isLoading = true; // Set loading to true right away

      // Ensure DOM has updated before fetching data
      await this.$nextTick();

      let payload = {
        params: {
          page: page,
          filter: {
            name: this.userName?.trim(),
            email: this.userEmail?.trim(),
          },
          sortBy: this.sortKey || undefined,
          sort: this.sortOrder || undefined,
          limit: this.limit,
        },
      };
      await this.getAllUsers(payload);

      this.currentPage = this.currentPageNumber;
      this.isLoading = false; // Set loading to false when done
      if (resolve) {
        resolve();
      }
    },
    fetchSortedData(options) {
      this.sortKey = options.sortKey || "";
      this.sortOrder = options.sortOrder || "";
      this.fetchDebouncedData();
    },
    searchByUserEmail(query) {
      this.userEmail = query;
      this.fetchDebouncedData();
    },
    searchByUserName(query) {
      this.userName = query;
      this.fetchDebouncedData();
    },
    handleClickNew() {
      this.update = false;
      this.showCreateForm = true;
    },
    handleClose() {
      this.update = false;
      this.showCreateForm = false;
    },
    handleEdit(user) {
      this.showCreateForm = true;
      this.update = true;
      this.selectedUserData = user;
    },
    handleDelte(user) {
      this.$bus.$emit("remove-confirmation-modal", {
        showModal: true,
        confirmationText: `This user (${user?.name}) and all of its references will be deleted pemanently.`,
        confirmEvent: "delete-user",
        id: user?._id,
      });
    },
  },
  beforeDestroy() {
    this.$bus.$off("refresh-users-list", this.updateHandler);
    this.$bus.$off("delete-user", this.deleteHandler);
    this.updateHandler = null;
    this.deleteHandler = null;
  },
};
</script>
