import Vue from "vue";

Vue.filter("toCurrency", (value, numberOfDecimals = 2) => {
  if (["To be added", "NA", "Not Available"].includes(value)) {
    return value;
  }

  // Handle empty, null, or undefined values
  if (value === null || value === undefined || value === "") {
    return ` 0${numberOfDecimals > 0 ? "," : ""}${"0".repeat(numberOfDecimals)}`;
  }

  // Parse the value to a number
  let number = parseFloat(value);
  if (isNaN(number)) {
    return ` 0${numberOfDecimals > 0 ? "," : ""}${"0".repeat(numberOfDecimals)}`; // Handle invalid numbers gracefully
  }

  // Format the number with specified decimals and apply EUR currency style
  const formatted = new Intl.NumberFormat("sfb", {
    style: "currency",
    currency: "EUR",
    minimumFractionDigits: numberOfDecimals,
    maximumFractionDigits: numberOfDecimals,
  }).format(number);

  // Ensure that the format matches " 1 200 540,00"
  return formatted.replace("", " ").replace(/,/g, " ").replace(/\./g, ",");
});
