import Vue from "vue";
import Router from "vue-router";
import store from "@/store";
import Login from "@/views/LoginPage.vue";
import Dashboard from "@/views/DashboardPage.vue";
import Category from "@/views/CategoryPage.vue";
import CompanyPage from "@/views/CompanyPage.vue";
import MarketResearchesPage from "@/views/MarketResearchesPage.vue";
import CompanyDetailsPage from "@/views/CompanyDetailsPage.vue";
import CalendarPage from "@/views/CalendarPage.vue";
import QuickPricePage from "@/views/QuickPricePage.vue";
import ForgotPasswordPage from "@/views/ForgotPasswordPage.vue";
import ResetPasswordPage from "@/views/ResetPasswordPage.vue";
import TodayPage from "@/views/TodayPage.vue";
import MarketResearchDetailsPage from "@/views/MarketResearchDetailsPage.vue";
import BulkAddPage from "@/views/BulkAddPage.vue";
import UserPanelPage from "@/views/UserPanelPage.vue";
import browserStorage from "@/utilities/helpers/storage";

Vue.use(Router);

const router = new Router({
  mode: "history",
  routes: [
    {
      path: "/login",
      name: "Login",
      component: Login,
    },
    {
      path: "/forgot-password",
      name: "ForgotPassword",
      component: ForgotPasswordPage,
      props: true,
    },
    {
      path: "/reset-password",
      name: "ResetPassword",
      component: ResetPasswordPage,
      props: true,
    },
    {
      path: "/dashboard",
      component: Dashboard,
      children: [
        {
          path: "",
          redirect: "companies",
        },
        {
          path: "companies",
          name: "Companies",
          component: CompanyPage,
        },
        {
          path: "category",
          name: "Category",
          component: Category,
        },
        {
          path: "companies/:id",
          name: "CompanyDetails",
          component: CompanyDetailsPage,
          props: true,
        },
        {
          path: "market-researches",
          name: "Researches",
          component: MarketResearchesPage,
        },
        {
          path: "market-researches/:id",
          name: "MarketResearchDetails",
          component: MarketResearchDetailsPage,
          props: true,
        },
        {
          path: "calendar",
          name: "Calendar",
          component: CalendarPage,
        },
        {
          path: "quick-price",
          name: "QuickPrice",
          component: QuickPricePage,
        },
        {
          path: "bulk-add",
          name: "BulkAdd",
          component: BulkAddPage,
        },
        {
          path: "user-panel",
          name: "UserPanel",
          component: UserPanelPage,
        },
        {
          path: "today",
          name: "Today",
          component: TodayPage,
        },
      ],
      meta: { requiresAuth: true },
    },
    {
      path: "*",
      redirect: "/login",
    },
  ],
});

// Navigation Guard to protect routes
router.beforeEach(async (to, from, next) => {
  try {
    let isAuthenticated = store.state.auth.isAuthenticated;
    const auth_token = browserStorage.get("auth_token");
    if (!isAuthenticated && auth_token) {
      await store.dispatch("auth/getProfile");
    }
    isAuthenticated = store.state.auth.isAuthenticated;
    if (
      to.path === "/login" ||
      to.path === "/reset-password" ||
      to.path === "/forgot-password"
    ) {
      if (isAuthenticated) {
        next("/dashboard");
      } else {
        next();
      }
    } else if (to.matched?.some((record) => record.meta.requiresAuth)) {
      if (!isAuthenticated && to.name !== "login") {
        next("/login");
      } else {
        next();
      }
    } else {
      next();
    }
  } catch (error) {
    console.log("error", error);
  }
});

export default router;
