import axiosInstance from "@/axiosInstance";
import { buildErrorMessage } from "@/utilities/helpers/utils";

export default {
  state: {
    error: null,
  },
  mutations: {
    SET_ERROR(state, payload) {
      state.error = payload;
    },
  },
  actions: {
    async getFileBlob({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.get("files/download", {
          responseType: "blob",
          params: payload?.params,
        });

        if (response?.data) {
          return {
            data: response?.data,
            message: response?.data?.message || "File downloaded successfully",
            success: response?.data?.success ?? true,
          };
        } else {
          return {
            message: "Unable to download file",
            success: false,
          };
        }
      } catch (error) {
        console.log(error);
        let errorPayload = {
          ...error,
          customMessage: "Unable to download file",
        };
        commit("SET_ERROR", errorPayload);
        return {
          success: false,
          message: buildErrorMessage(errorPayload),
        };
      }
    },
    async getTempFileViewLink({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.get("files/view", {
          params: payload?.params,
        });

        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Unable to generate temporary file link",
        };
        commit("SET_ERROR", errorPayload);
        return {
          success: false,
          message: buildErrorMessage(errorPayload),
        };
      }
    },
    async getOptions({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.get("options", {
          params: payload?.params,
        });

        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Unable to fetch  options",
        };
        commit("SET_ERROR", errorPayload);
        return {
          success: false,
          message: buildErrorMessage(errorPayload),
        };
      }
    },
  },
  getters: {
    getErrorMessage: (state) => buildErrorMessage(state.error),
  },
  namespaced: true,
};
