<template>
  <AuthWrapper :title="'Forgot Password'" class="min-h-screen">
    <form
      @submit.prevent="sendOTP"
      class="flex flex-1 flex-col overflow-y-auto px-spacing-300"
    >
      <div class="flex flex-1 flex-col gap-spacing-200 py-spacing-100">
        <FormInput
          ref="forgot-password-mail"
          :autofocus="true"
          v-model="email"
          input-type="email"
          label="Email"
          input-id="email"
          autocomplete="on"
          @enter-pressed="sendOTP"
          :field-validations="fieldValidations?.email"
        />
        <div
          class="text-content-on-status-on-info bg-bg-status-info border-stroke-info flex items-start gap-spacing-100 rounded-08 border px-spacing-100 py-spacing-50 text-body/m/relaxed/400"
        >
          <div>
            <IconInfo
              class="relative top-spacing-25 h-spacing-200 w-spacing-200 text-current"
            />
          </div>
          <p>
            Enter your registered email address, and well send a OTP to help
            you reset your password.
          </p>
        </div>
      </div>
      <div class="sticky bottom-0 bg-bg-neutral-default pt-spacing-200">
        <GeneralButton
          :button-type="'button'"
          :type="'brand'"
          :size="'large'"
          :emphasis="'primary'"
          :icon-position="'none'"
          :icon="''"
          :image="''"
          :is-disabled="false"
          :is-loading="loading"
          :label="'Send OTP'"
          @click.native="sendOTP"
        />
        <div class="mt-spacing-150 flex w-full justify-center">
          <RouterLink
            to="/login"
            class="text-center text-body/l/relaxed/400 text-constant-brand-primary"
          >
            Go to Login Page
          </RouterLink>
        </div>
      </div>
    </form>
  </AuthWrapper>
</template>

<script>
import { FormValidationRule } from "@/utilities/helpers/formValidations";
import { mapActions } from "vuex";
export default {
  components: {
    AuthWrapper: () => import("@/components/Auth/AuthWrapper.vue"),
    FormInput: () => import("@/components/General/FormInput.vue"),
    IconInfo: () => import("@/components/svgIcons/info.vue"),
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
  },
  data() {
    return {
      email: "",
      password: "",
      loading: false,

      fieldValidations: {
        email: new FormValidationRule({
          type: "EMAIL",
          required: true,
        }),
      },
    };
  },
  methods: {
    ...mapActions({
      forgotPassword: "auth/forgotPassword",
    }),
    async sendOTP() {
      let emailCopy = this.email;
      const isValid = this.$validateForm();
      if (!isValid) {
        return;
      }
      this.loading = true;
      const res = await this.forgotPassword(this.email);

      if (res?.success) {
        this.$toast.success(res?.message);
        this.$router.replace({
          name: "ResetPassword",
          params: { email: emailCopy },
        });
      } else {
        this.$toast.error(res?.message);
      }
      this.loading = false;
    },
  },
};
</script>
