import axiosInstance from "@/axiosInstance";
import { buildErrorMessage } from "@/utilities/helpers/utils";

export default {
  state: {
    companiesData: null,
    categoryData: null,
    error: null,
  },
  mutations: {
    SET_CATEGORIES_DATA(state, payload) {
      state.companiesData = payload;
    },
    SET_CATEGORY_DATA(state, payload) {
      state.categoryData = payload;
    },
    SET_ERROR(state, payload) {
      state.error = payload;
    },
  },
  actions: {
    async getCommercialItems({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.get(`commercial-items`, {
          params: payload?.params,
        });
        return response?.data;
      } catch (error) {
        commit("SET_ERROR", {
          ...error,
          customMessage:
            payload?.id === "all"
              ? "Unable to fetch Categories"
              : "Unable to fetch Sub Categories",
        });
      }
    },
    async getProductDetails({ commit }, payload) {
      commit("SET_ERROR", null);
      commit("SET_CATEGORY_DATA", null);
      try {
        const response = await axiosInstance.get(
          `commercial-items/${payload?.id}`,
          {
            params: payload?.params,
          },
        );
        commit("SET_CATEGORY_DATA", response?.data);
      } catch (error) {
        commit("SET_ERROR", {
          ...error,
          customMessage: "Unable to fetch Category Data",
        });
      }
    },
    async addCompaniesToProduct({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.post(
          `commercial-items/add-companies/${payload?.id}`,
          payload?.body,
        );
        return response?.data;
      } catch (error) {
        commit("SET_ERROR", {
          ...error,
          customMessage: "Unable to add category to company",
        });
      }
    },
    async getProductSuggestions(_, payload) {
      // commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.get(`commercial-items/search`, {
          params: payload?.params,
        });
        return response?.data?.data;
      } catch (error) {
        // commit("SET_ERROR", {
        //   ...error,
        //   customMessage: "Unable to fetch Category Data",
        // });
      }
    },
    async getProductVariables(_, payload) {
      // commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.get(`commercial-items/variables`, {
          params: payload?.params,
        });
        return response?.data;
      } catch (error) {
        // commit("SET_ERROR", {
        //   ...error,
        //   customMessage: "Unable to fetch Category Data",
        // });
      }
    },
    async addCommercialItem({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.post(`commercial-items`, payload);
        return response?.data;
      } catch (error) {
        commit("SET_ERROR", {
          ...error,
          customMessage: "Unable to add new category",
        });
      }
    },
    async updateCommercialItem({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.patch(
          `commercial-items/${payload?.id}`,
          payload?.body,
        );
        return response?.data;
      } catch (error) {
        commit("SET_ERROR", {
          ...error,
          customMessage: "Unable to update category",
        });
      }
    },
    async reorderCommercialItems({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.patch(
          `commercial-items/reorder`,
          payload?.body,
        );
        return response?.data;
      } catch (error) {
        const errorPayload = {
          ...error,
          customMessage: "Unable to reorder commercial items",
        };
        commit("SET_ERROR", errorPayload);
        return {
          message: buildErrorMessage(errorPayload),
          success: false,
        };
      }
    },
    async deleteCommercialItem({ commit }, payload) {
      commit("SET_ERROR", null);
      console.log("payload", payload);
      try {
        const response = await axiosInstance.delete(
          `commercial-items/${payload?.id}`,
          {
            params: payload?.params,
          },
        );
        return response?.data;
      } catch (error) {
        const errorPayload = {
          ...error,
          customMessage: "Unable to delete commercial item",
        };
        commit("SET_ERROR", errorPayload);
        return {
          message: buildErrorMessage(errorPayload),
          success: false,
        };
      }
    },
  },
  getters: {
    getErrorMessage: (state) => buildErrorMessage(state.error),
  },
  namespaced: true,
};
