import Vue from "vue";
import App from "@/App.vue";
import router from "@/router";
import store from "@/store";
import "@/tailwind.css";
import "@/jexcel-themes.css";
import "@/utilities/filters/utils";
import "@/utilities/filters/currencyFormatter";

import Toast from "vue-toastification";
import "vue-toastification/dist/index.css";
import { validateForm } from "./utilities/helpers/formValidations";

Vue.use(Toast, {
  bodyClassName: ["font-sans"],
  newestOnTop: true,
});
Vue.config.productionTip = false;
Vue.config.devtools = process.env.VUE_APP_DEV_TOOLS === "true" ? true : false;
Vue.prototype.$bus = new Vue(); // global event bus

Vue.prototype.$validateForm = validateForm;

Vue.prototype.$highlightMatch = (text, query) => {
  if (!text || !query) return text;
  const regex = new RegExp(`(${query?.trim()})`, "i"); // Removed 'g' flag
  return text?.replace(
    regex,
    '<span class="bg-blue-700 text-constant-neutral-white">$1</span>',
  );
};

if (localStorage.getItem("theme") === "light") {
  document.documentElement.classList.remove("dark");
} else {
  // Default to dark mode
  document.documentElement.classList.add("dark");
}

// Global Functions
Vue.prototype.$focusNextElement = (inputId) => {
  Vue.nextTick(() => {
    const nextInput = document.getElementById(inputId);
    if (nextInput) nextInput.focus();
  });
};

if (navigator.userAgent.indexOf("Windows") !== -1) {
  document.documentElement.classList.add("windows");
}

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
