<template>
  <div class="h-full overflow-y-auto bg-bg-neutral-default p-spacing-200">
    <CalendarComponent :events="events" />
  </div>
</template>

<script>
import axiosInstance from "@/axiosInstance";
export default {
  components: {
    CalendarComponent: () => import("@/components/Calendar/index.vue"),
  },
  data() {
    return { events: [] };
  },
  mounted() {
    this.fetchEvents();
  },
  methods: {
    async fetchEvents() {
      const res = await axiosInstance.get("/calendar/events");
      console.log(res.data);
      this.events = res?.data?.events;
    },
  },
};
</script>
