import axiosInstance from "@/axiosInstance";
import { buildErrorMessage } from "@/utilities/helpers/utils";

export default {
  state: {
    companiesData: null,
    companyData: null,
    error: null,
  },
  mutations: {
    SET_COMPANIES_DATA(state, payload) {
      // Preserve the existing companies in the state
      const loadedCompaniesList = state.companiesData?.companies || [];

      // Merge existing companies with new companies
      const updatedCompaniesList = [
        ...loadedCompaniesList,
        ...payload.companies,
      ];

      // Update the state with the new data, keeping other properties of payload intact
      state.companiesData = {
        ...payload,
        companies: updatedCompaniesList,
      };
    },
    RESET_COMPANIES_DATA(state) {
      state.companiesData = null;
    },
    SET_COMPANY_DATA(state, payload) {
      state.companyData = payload;
    },
    SET_ERROR(state, payload) {
      state.error = payload;
    },
  },
  actions: {
    async getCompanies({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.get(`companies`, {
          params: payload?.params,
        });
        if (response?.data?.currentPage === 1) {
          commit("RESET_COMPANIES_DATA");
        }
        commit("SET_COMPANIES_DATA", response?.data);
      } catch (error) {
        commit("SET_ERROR", {
          ...error,
          customMessage: "Unable to fetch Companies Data",
        });
      }
    },
    async getCompanyDetails({ commit }, payload) {
      commit("SET_ERROR", null);
      commit("SET_COMPANY_DATA", null);
      try {
        const response = await axiosInstance.get(`companies/${payload?.id}`, {
          params: payload?.params,
        });
        commit("SET_COMPANY_DATA", response?.data);
      } catch (error) {
        commit("SET_ERROR", {
          ...error,
          customMessage: "Unable to fetch Companies Data",
        });
      }
    },
    async addCompanies({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.post(`companies`, payload);
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage:
            payload?.length < 1
              ? "Unable to add new company"
              : "Unable to add new companies",
        };
        commit("SET_ERROR", errorPayload);
        return {
          message: buildErrorMessage(errorPayload),
          success: false,
        };
      }
    },

    async addProductToCompany({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.post(
          `companies/add-products/${payload?.id}`,
          payload?.body,
        );
        return response?.data;
      } catch (error) {
        commit("SET_ERROR", {
          ...error,
          customMessage: "Unable to add category to company",
        });
      }
    },
    async updateCompany({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.patch(
          `companies/${payload?.id}`,
          payload?.body,
        );
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: " Unable to update company",
        };
        commit("SET_ERROR", errorPayload);
        return {
          message: buildErrorMessage(errorPayload),
          success: false,
        };
      }
    },

    async deleteCompany({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.delete(`companies/${payload?.id}`);
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Unable to delete company",
        };
        commit("SET_ERROR", errorPayload);
        return {
          message: buildErrorMessage(errorPayload),
          success: false,
        };
      }
    },

    async checkCompanyNameAvailabilty({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.get(
          `companies/check-for-name-availabilty`,
          {
            params: payload?.params,
          },
        );
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Some Error Occured",
        };
        commit("SET_ERROR", errorPayload);
        return {
          message: buildErrorMessage(errorPayload),
          success: false,
        };
      }
    },

    async getCompanySuggestions(_, payload) {
      // commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.get(`companies/search`, {
          params: payload?.params,
        });
        return response?.data?.data;
      } catch (error) {
        // commit("SET_ERROR", {
        //   ...error,
        //   customMessage: "Unable to fetch Category Data",
        // });
      }
    },
    async getCompaniesByProducts(_, payload) {
      // commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.post(
          `companies/companies-by-products`,
          payload,
        );
        return response?.data?.data;
      } catch (error) {
        // commit("SET_ERROR", {
        //   ...error,
        //   customMessage: "Unable to fetch Category Data",
        // });
      }
    },
    async addQuickPrice({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.post(
          `quotations/quick-price`,
          payload,
        );
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Unable to add quick price",
        };
        commit("SET_ERROR", errorPayload);
        return {
          message: buildErrorMessage(errorPayload),
          success: false,
        };
      }
    },
  },
  getters: {
    getErrorMessage: (state) => buildErrorMessage(state.error),
  },
  namespaced: true,
};
