<template>
  <div class="flex h-full w-full flex-col bg-bg-neutral-default">
    <div
      class="flex w-full justify-center bg-bg-neutral-default py-spacing-300"
    >
      <div
        class="mx-spacing-200 flex w-full max-w-[800px] items-center rounded-08 border-4 border-bg-neutral-sunken bg-bg-neutral-sunken px-spacing-0 py-spacing-0 text-body/l/relaxed/400 shadow-inner dark:border-bg-neutral-sunken"
      >
        <div
          v-for="(tab, index) in tabs"
          :key="index"
          :class="[
            'flex-1 whitespace-nowrap rounded-06 px-spacing-150 py-spacing-100 text-center',
            tab.isActive
              ? 'bg-bg-neutral-default text-content-on-neutral-primary dark:bg-bg-neutral-raised'
              : 'cursor-pointer text-content-on-neutral-tertiary',
          ]"
          @click="selectTab(index)"
        >
          {{ tab.label }}
        </div>
      </div>
    </div>

    <div v-if="Object.keys(groupedEvents)?.length > 0" class="pb-spacing-300">
      <div
        v-for="(events, key, index) in groupedEvents"
        :key="key"
        class="flex w-full flex-col gap-spacing-50 px-spacing-200 ipad:px-spacing-300"
        :class="index > 0 ? 'mt-spacing-400' : ''"
      >
        <div
          class="sticky top-spacing-0 z-10 flex items-center gap-spacing-200 bg-bg-neutral-default py-spacing-300"
        >
          <span
            v-if="key === 'QUOTATION_FOLLOW_UP_EVENT'"
            class="text-body/l/relaxed/400 text-content-on-neutral-primary"
            >Research Follow Ups</span
          >
          <span
            v-else-if="key === 'COMPANY_DETAILS_MISSING'"
            class="text-body/l/relaxed/400 text-content-on-neutral-primary"
            >Missing Company Details</span
          >
          <span class="flex-1 border-b border-constant-brand-primary" />
        </div>

        <div class="flex flex-col gap-spacing-100">
          <div
            class="flex w-full items-center justify-between gap-spacing-200 rounded-08 bg-bg-neutral-raised px-spacing-200 py-spacing-150"
            v-for="event in events"
            :key="event?._id"
          >
            <p
              class="flex flex-col gap-spacing-50 text-content-on-neutral-primary"
            >
              <span
                v-if="tabs[1]?.isActive"
                class="text-content-on-status-on-info bg-bg-status-info border-stroke-info mb-spacing-50 self-start rounded-08 border px-spacing-100 py-spacing-50 text-body/s/relaxed/400"
                >{{ event?.eventDate | toDate("DD MMM YYYY") }}</span
              >
              <span
                v-if="event.type === 'COMPANY_DETAILS_MISSING'"
                class="text-body/m/relaxed/400"
              >
                Add Missing Company Details
              </span>
              <span v-else class="text-body/m/relaxed/400">{{
                event?.marketResearchId?.name
              }}</span>
              <span
                class="text-body/s/relaxed/400 text-content-on-neutral-secondary"
                >{{ event?.companyId?.companyName }}</span
              >
            </p>
            <div
              v-if="event.status === 'COMPLETED'"
              class="mr-spacing-200 flex h-spacing-300 w-spacing-300 items-center justify-center rounded-full bg-constant-status-success"
            >
              <IconCheck
                class="h-spacing-250 w-spacing-250 text-constant-neutral-white"
              />
            </div>
            <div v-else class="flex items-center gap-spacing-150">
              <GeneralButton
                v-if="event.type === 'COMPANY_DETAILS_MISSING'"
                :button-type="'button'"
                :type="'success'"
                :size="'small'"
                :emphasis="'secondary'"
                :icon-position="'none'"
                :label="'Change Date'"
                @click.native="changeEventDate(event)"
              />
              <GeneralButton
                :button-type="'button'"
                :type="'brand'"
                :size="'small'"
                :emphasis="'secondary'"
                :icon-position="'none'"
                :label="key === 'QUOTATION_FOLLOW_UP_EVENT' ? 'Start' : 'Add'"
                @click.native="go(event)"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="flex h-full items-center justify-center">
      <div class="flex flex-col items-center gap-spacing-200">
        <IconCalendar
          class="h-spacing-600 w-spacing-600 text-content-on-neutral-tertiary"
        />
        <p
          v-if="tabs[0].isActive"
          class="text-headline/s/relaxed/500 text-content-on-neutral-tertiary"
        >
          No events for today!
        </p>
        <p
          v-else-if="tabs[1].isActive"
          class="text-headline/s/relaxed/500 text-content-on-neutral-tertiary"
        >
          No overdue events!
        </p>
      </div>
    </div>
    <ChangeEventDateModal />
  </div>
</template>

<script>
import axiosInstance from "@/axiosInstance";
export default {
  components: {
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
    IconCheck: () => import("@/components/svgIcons/check.vue"),
    ChangeEventDateModal: () =>
      import("@/components/Modals/ChangeEventDateModal.vue"),
    IconCalendar: () => import("@/components/svgIcons/calendar.vue"),
  },
  data() {
    return {
      overdueEvents: [],
      todayEvents: [],
      tabs: [
        { label: "Today", isActive: true },
        { label: "Overdue", isActive: false },
      ],
    };
  },

  computed: {
    groupedEvents() {
      let events = this.todayEvents;
      if (this.tabs[1]?.isActive) {
        events = this.overdueEvents;
      }
      return events.reduce((acc, event) => {
        if (!acc[event?.type]) {
          acc[event?.type] = [];
        }
        acc[event?.type].push(event);
        return acc;
      }, {});
    },
  },
  mounted() {
    this.$bus.$on("refetch-today-events", () => {
      this.fetchTodayEvents();
    });
    this.fetchTodayEvents();
  },
  methods: {
    selectTab(index) {
      this.tabs.forEach((tab, i) => {
        tab.isActive = i === index;
      });
    },
    async fetchTodayEvents() {
      this.$bus.$emit("showLoader", {
        showLoader: true,
      });
      const res = await axiosInstance.get("/calendar/events/today-and-overdue");
      this.$bus.$emit("showLoader", false);
      this.overdueEvents = res?.data?.overdueEvents;
      this.todayEvents = res?.data?.todayEvents;
    },
    changeEventDate(event) {
      this.$bus.$emit("change-event-date-modal", { showModal: true, event });
    },
    go(event) {
      if (event.type === "QUOTATION_FOLLOW_UP_EVENT") {
        this.$router.push({
          path: `/dashboard/market-researches/${event?.marketResearchId?._id}`,
          query: {
            companyId: event?.companyId?._id,
          },
        });
      } else if (event.type === "COMPANY_DETAILS_MISSING") {
        this.$router.push({
          path: `/dashboard/companies/${event?.companyId?._id}`,
          query: {
            edit: true,
          },
        });
      }
    },
  },
  beforeDestroy() {
    this.$bus.$off("refetch-today-events");
  },
};
</script>
