import axiosInstance from "@/axiosInstance";
import { buildErrorMessage } from "@/utilities/helpers/utils";
import browserStorage from "@/utilities/helpers/storage";

export default {
  state: {
    user: null,
    token: null,
    error: null,
    isAuthenticated: false,
    trackers: {},
    isAuthLoading: false,
    usersData: null,
  },
  mutations: {
    SET_USERS_DATA(state, payload) {
      // Preserve the existing users in the state
      const loadedUsersList = state.usersData?.users || [];

      // Merge existing users with new users
      const updatedUsersList = [...loadedUsersList, ...payload.users];

      // Update the state with the new data, keeping other properties of payload intact
      state.usersData = {
        ...payload,
        users: updatedUsersList,
      };
    },
    RESET_USERS_DATA(state) {
      state.usersData = null;
    },
    SET_USER(state, user) {
      state.user = user;
    },
    SET_USER_TRACKERS(state, trackers) {
      state.trackers = trackers;
    },
    SET_TOKEN(state, token) {
      state.token = token;
      if (token) {
        browserStorage.set("auth_token", token);
      }
      state.isAuthenticated = token ? true : false;
    },
    SET_ERROR(state, payload) {
      state.error = payload;
    },
    CLEAR_AUTH(state) {
      state.user = null;
      state.token = null;
      state.isAuthenticated = false;
    },
    AUTH_LOADING(state, payload) {
      state.isAuthLoading = payload;
    },
  },
  actions: {
    async getAllUsers({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.get(`auth/all-users`, {
          params: payload?.params,
        });
        if (response?.data?.data?.currentPage === 1) {
          commit("RESET_USERS_DATA");
        }
        commit("SET_USERS_DATA", response?.data?.data);
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Unable to fetch Users Data",
        };
        commit("SET_ERROR", errorPayload);
        return { message: buildErrorMessage(errorPayload), success: false };
      }
    },

    async login({ commit }, credentials) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.post("auth/login", credentials);
        commit("SET_USER", response?.data?.user);
        commit("SET_TOKEN", response?.data?.token);
        commit("SET_USER_TRACKERS", response?.data?.user?.trackers);
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Login failed",
        };
        commit("SET_ERROR", errorPayload);
        return { message: buildErrorMessage(errorPayload), success: false };
      }
    },

    async updateUserByID(_, payload) {
      try {
        const response = await axiosInstance.patch(
          `auth/update-user/${payload?.id}`,
          payload?.body,
        );
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Unable to update user details",
        };
        return { message: buildErrorMessage(errorPayload), success: false };
      }
    },

    async deleteUserByID(_, userId) {
      try {
        const response = await axiosInstance.delete(
          `auth/delete-user/${userId}`,
        );
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Unable to delete user",
        };
        return { message: buildErrorMessage(errorPayload), success: false };
      }
    },

    async getProfile({ commit }) {
      commit("AUTH_LOADING", true);
      try {
        const response = await axiosInstance.get("auth/me");
        if (response?.data?.success) {
          commit("SET_USER", response?.data?.user);
          commit("SET_TOKEN", response?.data?.token);
          commit("SET_USER_TRACKERS", response?.data?.user?.trackers);
        }
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Unable to get Profile",
        };
        commit("SET_ERROR", errorPayload);
        return { message: buildErrorMessage(errorPayload), success: false };
      } finally {
        commit("AUTH_LOADING", false);
      }
    },

    async getTrackers({ commit }) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.get("auth/trackers");
        if (response?.data?.success) {
          commit("SET_USER_TRACKERS", response?.data?.trackers);
        }
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Login failed",
        };
        return { message: buildErrorMessage(errorPayload), success: false };
      }
    },

    async signup({ commit }, userDetails) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.post("auth/signup", userDetails);
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Signup failed",
        };
        commit("SET_ERROR", errorPayload);
        return { message: buildErrorMessage(errorPayload), success: false };
      }
    },

    async forgotPassword({ commit }, email) {
      commit("SET_ERROR", null);
      try {
        const res = await axiosInstance.post("auth/forgot-password", { email });
        return res?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Failed to send password reset email",
        };
        commit("SET_ERROR", errorPayload);
        return { message: buildErrorMessage(errorPayload), success: false };
      }
    },

    async resetPassword({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const res = await axiosInstance.post(
          `auth/verify-otp-reset-password`,
          payload,
        );
        return res?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Password reset failed",
        };
        commit("SET_ERROR", errorPayload);
        return { message: buildErrorMessage(errorPayload), success: false };
      }
    },

    async logout({ commit }) {
      try {
        const response = await axiosInstance.post("auth/logout");
        commit("CLEAR_AUTH");
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Logout failed",
        };
        commit("SET_ERROR", errorPayload);
        return { message: buildErrorMessage(errorPayload), success: false };
      }
    },

    async getUserSuggestions(_, payload) {
      try {
        const response = await axiosInstance.get(`auth/users`, {
          params: payload?.params,
        });
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Unable to get user suggestions",
        };
        return { message: buildErrorMessage(errorPayload), success: false };
      }
    },
  },
  getters: {
    getUser: (state) => state.user,
    getUserTrackers: (state) => state.trackers,
    getErrorMessage: (state) => buildErrorMessage(state.error),
    getIsAuthLoading: (state) => state.isAuthLoading,
  },
  namespaced: true,
};
