<template>
  <div
    class="flex h-full w-full flex-col bg-bg-neutral-default ipadPro:flex-row"
  >
    <div
      class="flex w-full flex-1 flex-col overflow-y-auto pt-spacing-300 ipadPro:w-1/2"
    >
      <div
        class="flex w-full flex-col gap-spacing-200 px-spacing-200 ipad:px-spacing-300"
      >
        <div class="flex w-full items-center gap-spacing-200">
          <h2
            class="cursor-pointer text-overline/l/relaxed/500 text-content-on-neutral-secondary hover:text-content-on-neutral-primary ipad:text-overline/xl/relaxed/500"
            @click="openSideBar()"
          >
            COMPANIES
          </h2>
          <div
            @click="openAddCompanyModal"
            class="flex h-spacing-250 w-spacing-250 cursor-pointer items-center justify-center rounded-full bg-constant-brand-primary"
          >
            <IconPlus
              class="h-spacing-200 w-spacing-200 text-constant-neutral-white"
            />
          </div>
          <div
            class="fixed right-spacing-300 top-spacing-300 ipadPro:hidden"
            @click="openCompanyMapModal"
          >
            <GeneralButton
              :button-type="'button'"
              :type="'brand'"
              :size="'small'"
              :emphasis="'secondary'"
              :icon-position="'none'"
              :label="'Show On Map'"
            />
          </div>
        </div>
        <div class="flex flex-col gap-spacing-150">
          <div
            v-if="products?.length > 0"
            class="flex w-full flex-wrap gap-spacing-150"
          >
            <GeneralRemovableItem
              v-for="product in products"
              :key="product?._id"
              :item-name="product?.originalTitle"
              @on-item-removed="removeProductFromProducts(product)"
            />
          </div>
          <!-- Product Search Field -->
          <ProductSearchDropdown
            :dropdown-label="'Products'"
            :dropdown-id="'products'"
            :products="products"
            :is-multi-select="true"
            @on-products-update="updateProducts"
            :show-error="false"
            :show-selected-list="false"
            class="mb-spacing-50"
          />
          <div
            class="flex flex-col gap-spacing-200 nexus:flex-row ipad:gap-spacing-150"
          >
            <Search
              placeholder="Search..."
              @search="searchByCompanyName"
              label="Company"
              search-id="company-name-search"
            />
            <Search
              placeholder="Search..."
              @search="searchByGS"
              label="GS"
              search-id="general-score-search"
            />
          </div>
        </div>
      </div>
      <PaginatedTable
        :headers="headerData"
        :current-page="currentPage"
        :total-pages="totalPages"
        :sort-key="sortKey"
        :sort-order="sortOrder"
        :total-count="totalCount"
        :error="error"
        :is-loading="isLoading"
        @fetch-data="fetchData"
        @fetch-sorted-data="fetchSortedData"
      >
        <template #table-body>
          <tr
            v-for="item in getMappedData"
            :key="item.id"
            class="cursor-pointer text-left text-body/m/relaxed/400 text-content-on-neutral-primary nexus:text-body/l/relaxed/400"
            @click="goToCompany(item)"
          >
            <td
              class="flex flex-col gap-spacing-25 border-b border-stroke-medium py-spacing-100 pr-spacing-100"
            >
              <span v-html="$highlightMatch(item?.companyName, companyName)" />
              <p class="min-h-spacing-250">
                <span
                  v-for="(product, index) in item?.products"
                  :key="index"
                  class="text-body/s/relaxed/400 text-content-on-neutral-tertiary nexus:text-body/m/relaxed/400"
                  ><span v-if="index > 0"> | </span
                  ><span>{{ product?.title }}</span>
                </span>
              </p>
            </td>
            <td
              class="border-b border-stroke-medium py-spacing-100 pr-spacing-100"
            >
              <p v-html="$highlightMatch(String(item?.GS ?? 0), companyGS)" />
            </td>
            <td
              class="border-b border-stroke-medium py-spacing-100 pr-spacing-100"
            >
              {{ item?.RS ?? 0 }}
            </td>
            <td
              class="whitespace-nowrap border-b border-stroke-medium py-spacing-100 pr-spacing-100 text-content-on-neutral-secondary"
            >
              {{ item?.annualRevenue | toCurrency(0) }}
            </td>
          </tr>
          <tr
            v-show="isLoading"
            v-for="(_, row) in new Array(6).fill('')"
            :key="`dummy-row-${row}`"
            class="text-left"
          >
            <td
              v-for="(_, col) in new Array(4).fill('')"
              :key="`dummy-col-${col}`"
              class="gap-spacing-25 py-spacing-150 pr-spacing-100"
            >
              <GeneralSkeleton
                :is-table-cell="true"
                :class="
                  col < 1 || col > 2
                    ? 'h-spacing-300 w-spacing-1200'
                    : 'h-spacing-300 w-spacing-400'
                "
              />
            </td>
          </tr>
        </template>
      </PaginatedTable>
    </div>

    <div class="hidden h-full w-full p-spacing-300 ipadPro:block ipadPro:w-1/2">
      <MapComponent
        :markers-data="companies"
        :map-container-id="'company-map-on-page'"
      />
    </div>
    <AddNewCompanyModal />
    <AddDetailsLaterModal />
    <CompanyMapModal />
  </div>
</template>

<script>
import { debounce } from "lodash";
import { mapActions, mapMutations, mapState } from "vuex";

export default {
  components: {
    IconPlus: () => import("@/components/svgIcons/plus.vue"),
    AddNewCompanyModal: () => import("@/components/Modals/AddNewCompany.vue"),
    Search: () => import("@/components/General/Search.vue"),
    PaginatedTable: () =>
      import("@/components/General/Table/PaginatedTable.vue"),
    MapComponent: () => import("@/components/MapComponent/index.vue"),
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
    CompanyMapModal: () => import("@/components/Modals/CompanyMapModal.vue"),
    GeneralSkeleton: () => import("@/components/General/Skeleton.vue"),
    ProductSearchDropdown: () =>
      import("@/components/Utility/CustomDropdowns/ProductSearch.vue"),
    GeneralRemovableItem: () =>
      import("@/components/General/RemovableItem.vue"),
    AddDetailsLaterModal: () =>
      import("@/components/Modals/AddDetailsLaterModal.vue"),
  },
  data() {
    return {
      products: [],
      companyGS: "",
      companyName: "",
      isLoading: false,
      sortKey: "",
      sortOrder: "",
      currentPage: 0,
      headerData: [
        { key: "companyName", value: "Company Name", sortable: true },
        {
          key: "GS",
          value: "GS",
          sortable: true,
          description: "General Scoring",
        },
        {
          key: "RS",
          value: "RS",
          sortable: true,
          description: "Retail Scoring",
        },
        {
          key: "annualRevenue",
          value: "AR",
          sortable: true,
          description: "Annual Revenue",
        },
      ],
      fetchDebouncedData: debounce(this.fetchData, 300),
    };
  },
  mounted() {
    if (this.companiesData) {
      this.RESET_COMPANIES_DATA();
    }
    this.$bus.$on("refetch-companies", () => {
      this.fetchDebouncedData();
    });
    this.fetchDebouncedData();
  },
  computed: {
    ...mapState({
      companiesData: (state) => state.company?.companiesData,
      companies: (state) => state.company?.companiesData?.companies,
      currentPageNumber: (state) =>
        state.company?.companiesData?.currentPage || 0,
      totalPages: (state) => state.company?.companiesData?.totalPages || 1,
      totalCount: (state) => state.company?.companiesData?.totalCount || 0,
      error: (state) => state.company?.error,
    }),
    getMappedData() {
      let output = [];
      if (this.companies?.length > 0) {
        output = this.companies?.map((company) => {
          let { products, ...remaining } = company;
          let companyOutput = { ...remaining };
          if (products) {
            companyOutput.products = products
              ?.map((item) => ({
                title: item?.productId?.title,
                _id: item?.productId?._id,
              }))
              ?.filter((item) => item?.title);
          }
          return companyOutput;
        });
      }
      return output;
    },
    getProductIds() {
      let output = [];
      output = this.products?.map((item) => item?._id);
      return output;
    },
  },
  methods: {
    ...mapMutations("company", ["RESET_COMPANIES_DATA"]),
    ...mapActions("company", ["getCompanies"]),

    async fetchData(page = 1, resolve) {
      if (this.isLoading) {
        return;
      }
      this.currentPage = page;
      this.isLoading = true; // Set loading to true right away

      // Ensure DOM has updated before fetching data
      await this.$nextTick();

      let payload = {
        params: {
          page: page,
          filter: {
            companyName: this.companyName?.trim(),
            companyGS: this.companyGS?.trim(),
            products: this.getProductIds,
          },
          sort: this.sortKey || undefined,
          order: this.sortOrder || undefined,
        },
      };
      await this.getCompanies(payload);

      this.currentPage = this.currentPageNumber;
      this.isLoading = false; // Set loading to false when done
      if (resolve) {
        resolve();
      }
    },
    fetchSortedData(options) {
      this.sortKey = options.sortKey || "";
      this.sortOrder = options.sortOrder || "";
      this.fetchDebouncedData();
    },
    openAddCompanyModal() {
      this.$bus.$emit("add-new-company-modal", { showModal: true });
    },
    goToCompany(item) {
      this.$router.push({ path: `/dashboard/companies/${item._id}` });
    },
    openCompanyMapModal() {
      this.$bus.$emit("company-map-modal", {
        showModal: true,
        markersData: this.companies,
      });
    },
    openSideBar() {
      this.$bus.$emit("open-sidebar");
    },
    updateProducts(products) {
      this.products = products;
      this.fetchDebouncedData();
    },
    removeProductFromProducts(product) {
      this.products = this.products?.filter(
        (item) => item?._id !== product?._id,
      );
      this.fetchDebouncedData();
    },
    searchByGS(query) {
      this.companyGS = query;
      this.fetchDebouncedData();
    },
    searchByCompanyName(query) {
      this.companyName = query;
      this.fetchDebouncedData();
    },
  },
  beforeDestroy() {
    this.$bus.$off("refetch-companies");
  },
};
</script>

<style></style>
