<template>
  <div
    v-if="loading || getIsAuthLoading"
    class="fixed inset-0 z-[999999] flex h-screen w-full items-center justify-center"
    :class="loading ? 'bg-bg-overlay' : ''"
  >
    <div class="flex flex-col items-center justify-center gap-spacing-150">
      <div
        class="h-spacing-700 w-spacing-700 animate-spin text-constant-brand-primary"
      >
        <IconLoader2 class="h-full w-full" />
      </div>
      <p
        v-if="loaderText"
        class="text-body/l/relaxed/400 text-content-on-neutral-primary"
      >
        {{ loaderText
        }}<span class="inline-block animate-dot1 text-headline/m/relaxed/500"
          >.</span
        >
        <span class="inline-block animate-dot2 text-headline/m/relaxed/500"
          >.</span
        >
        <span class="inline-block animate-dot3 text-headline/m/relaxed/500"
          >.</span
        >
      </p>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  data: () => ({
    loading: false,
    loaderText: "",
  }),
  computed: {
    ...mapGetters({
      getIsAuthLoading: "auth/getIsAuthLoading",
    }),
  },
  components: {
    IconLoader2: () => import("@/components/svgIcons/loader-2.vue"),
  },
  methods: {
    start() {
      this.loading = true;
    },
    finish() {
      this.loading = false;
    },
  },
  mounted() {
    this.$bus.$on("showLoader", (payload) => {
      if (payload?.showLoader === true) {
        this.loaderText = payload?.loaderText;
        this.start();
      } else if (payload === false) {
        this.finish();
      }
    });
  },
  beforeDestroy() {
    this.loading = null;
    delete this.loading;
    this.$bus.$off("showLoader");
  },
};
</script>
