<template>
  <div class="m-auto h-full w-full max-w-[800px] bg-bg-neutral-default">
    <GeneralForm
      :heading="'Quick Price'"
      :sub-heading="'Add quick price for a product'"
      :showCloseButton="false"
      @form-submitted="handleSubmit"
      :submit-button-label="'Add'"
      :submit-button-type="'success'"
      :submit-button-disabled="!isValid"
      :is-form-submitting="isFormSubmitting"
      class="flex-1"
    >
      <template #form-body>
        <div class="mt-spacing-300 flex w-full flex-col gap-spacing-200">
          <CompanySearchDropdown
            :dropdown-label="'Company'"
            :dropdown-id="'company'"
            :selected-company="formData.company"
            @on-company-update="updateCompany"
          />
          <ProductSearchDropdown
            :dropdown-label="'Product'"
            :dropdown-id="'product'"
            :selected-product="formData.product"
            :company-id="formData.company?._id"
            @on-product-update="updateProduct"
            :show-select-button="false"
          />
          <GeneralSelect
            label="Variable"
            :options="variableOptions"
            @option-selected-full="handleVariableSelect"
          />

          <div
            class="flex w-full flex-col gap-spacing-200 ipad:flex-row ipad:items-center"
          >
            <FormInput
              label="Price"
              v-model="formData.price"
              input-type="number"
              inputId="price"
            />
            <FormInput
              label="Date Add"
              v-model="formData.date"
              input-type="date"
            />
          </div>
        </div>
      </template>
    </GeneralForm>
  </div>
</template>

<script>
import { getDateFromToday } from "@/utilities/helpers/utils";
import { mapActions } from "vuex";
export default {
  components: {
    FormInput: () => import("@/components/General/FormInput.vue"),
    GeneralForm: () => import("@/components/General/Form.vue"),
    CompanySearchDropdown: () =>
      import("@/components/Utility/CustomDropdowns/CompanySearch.vue"),
    ProductSearchDropdown: () =>
      import("@/components/Utility/CustomDropdowns/ProductSearch.vue"),
    GeneralSelect: () => import("@/components/General/Select.vue"),
  },
  data() {
    return {
      formData: {
        company: null,
        product: null,
        variable: null,
        price: "",
        date: getDateFromToday(0),
      },
      productVariables: null,
      isFormSubmitting: false,
    };
  },
  computed: {
    isValid() {
      let output = false;
      if (
        this.formData?.company?._id &&
        this.formData?.date &&
        this.formData?.price &&
        this.formData?.product?._id
      ) {
        output = true;
      }
      return output;
    },
    variableOptions() {
      let output = [];
      if (
        this.productVariableOptions &&
        Object.keys(this.productVariableOptions)?.length > 0
      ) {
        output = this.productVariableOptions[this.formData?.product?._id] || [];
      }
      return output;
    },
    productVariableOptions() {
      if (
        !this.productVariables ||
        !Object.keys(this.productVariables).length
      ) {
        return {};
      }

      return Object.entries(this.productVariables)?.reduce(
        (output, [key, items]) => {
          output[key] = [
            ...items
              ?.filter(
                (item) =>
                  !this.productPrices?.some(
                    (price) => price.variableId === item?._id,
                  ),
              )
              ?.map((item) => ({
                key: item?._id,
                value: `${item.name} [${item.coefficient}]`,
              })),
            {
              key: "",
              value: `default [1]`,
              default: true,
            },
          ];
          return output;
        },
        {},
      );
    },
  },
  methods: {
    ...mapActions("category", ["getProductVariables"]),
    ...mapActions("company", ["addQuickPrice"]),
    async handleSubmit() {
      this.isFormSubmitting = true;
      let payload = {
        company: this.formData?.company?._id,
        product: this.formData?.product?._id,
        variable: this.formData?.variable?.id,
        price: this.formData?.price,
        date: this.formData?.date,
      };
      const res = await this.addQuickPrice(payload);
      if (res?.success) {
        this.formData = {
          company: null,
          product: null,
          variable: null,
          price: "",
          date: getDateFromToday(0),
        };
        this.$toast.success(res?.message);
      } else {
        this.$toast.error(res?.message);
      }
      this.isFormSubmitting = false;
    },
    updateCompany(company) {
      this.formData.company = company;
      this.formData.product = null;
    },
    async updateProduct(product) {
      this.formData.product = product;
      const res = await this.getProductVariables({
        params: {
          productIds: [product?._id],
        },
      });
      if (res?.success) {
        this.productVariables = res?.data;
      }
    },
    handleVariableSelect(variable) {
      this.formData.variable = {
        id: variable?.key,
        name: variable?.value,
      };
    },
  },
};
</script>
