const STORAGE_TYPES = {
  LOCAL: localStorage,
  SESSION: sessionStorage,
};

const browserStorage = (() => {
  // Get data from storage
  const get = (key, storage = STORAGE_TYPES.LOCAL) => {
    try {
      const value = storage.getItem(key);
      return value ? JSON.parse(value) : null;
    } catch (error) {
      console.error("Error getting data from storage", error);
      return null;
    }
  };

  // Set data in storage
  const set = (key, value, storage = STORAGE_TYPES.LOCAL) => {
    try {
      storage.setItem(key, JSON.stringify(value));
    } catch (error) {
      console.error("Error setting data in storage", error);
    }
  };

  // Remove data from storage
  const remove = (key, storage = STORAGE_TYPES.LOCAL) => {
    try {
      storage.removeItem(key);
    } catch (error) {
      console.error("Error removing data from storage", error);
    }
  };

  // Clear all data from storage
  const clear = (storage = STORAGE_TYPES.LOCAL) => {
    try {
      storage.clear();
    } catch (error) {
      console.error("Error clearing storage", error);
    }
  };

  // Check if a key exists in storage
  const exists = (key, storage = STORAGE_TYPES.LOCAL) => {
    try {
      return storage.getItem(key) !== null;
    } catch (error) {
      console.error("Error checking existence of key in storage", error);
      return false;
    }
  };

  return {
    get,
    set,
    remove,
    clear,
    exists,
    STORAGE_TYPES, // Expose STORAGE_TYPES for use in other components
  };
})();

export default browserStorage;
