<template>
  <div
    class="min-w-screen sidebar fixed left-spacing-0 top-spacing-0 z-[5000] flex h-screen max-h-screen w-screen flex-col overflow-y-auto text-constant-neutral-white shadow-lg nexus:static nexus:w-spacing-800 nexus:min-w-spacing-800 nexus:animate-none ipad:w-spacing-1300 ipad:min-w-spacing-1300"
    :class="[
      open ? 'animate-slideIn' : 'animate-slideOut',
      showSideBar ? '' : 'opacity-0 nexus:opacity-100',
    ]"
  >
    <div @click="closeSideBar()" :class="open ? 'nexus:hidden' : 'hidden'">
      <IconX
        class="absolute right-spacing-200 top-spacing-200 h-spacing-300 w-spacing-300 cursor-pointer text-content-on-neutral-secondary hover:text-content-on-neutral-primary"
      />
    </div>
    <ul class="flex h-full flex-1 flex-col text-body/m/relaxed/400">
      <li class="py-spacing-50">
        <div
          class="sidebar-tile w-full cursor-pointer items-center py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-100"
          @click="toggleTheme()"
          tabindex="0"
        >
          <IconMoonStar
            v-show="isDarkMode"
            class="h-spacing-250 w-spacing-250 text-constant-neutral-white"
          />
          <IconSun
            v-show="!isDarkMode"
            class="h-spacing-250 w-spacing-250 text-constant-neutral-white"
          />
        </div>
      </li>
      <li class="flex w-full flex-1 items-center">
        <RouterLink
          to="/dashboard/companies"
          class="sidebar-tile flex-1 items-center py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-100"
        >
          <i class="fa fa-building" />
          <span class="text-center nexus:hidden ipad:block">Companies</span>
        </RouterLink>
      </li>
      <li class="flex w-full flex-1 items-center">
        <RouterLink
          to="/dashboard/category"
          class="sidebar-tile items-center py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-100"
        >
          <i class="fa fa-list" />
          <span class="text-center nexus:hidden ipad:block">Categories</span>
        </RouterLink>
      </li>
      <li class="flex w-full flex-1 items-center">
        <RouterLink
          to="/dashboard/market-researches"
          class="sidebar-tile items-center py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-100"
        >
          <i class="fa fa-search" />
          <span class="text-center nexus:hidden ipad:block"
            >Market Researches</span
          >
        </RouterLink>
      </li>
      <li class="flex w-full flex-1 items-center">
        <RouterLink
          to="/dashboard/calendar"
          class="sidebar-tile items-center py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-100"
        >
          <i class="fa fa-calendar" />
          <span class="text-center nexus:hidden ipad:block">Calendar</span>
        </RouterLink>
      </li>
      <li class="flex w-full flex-1 items-center">
        <RouterLink
          to="/dashboard/today"
          class="sidebar-tile items-center py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-100"
        >
          <i class="fa fa-sun" />
          <span class="text-center nexus:hidden ipad:block">Today</span>
        </RouterLink>
      </li>
      <div
        class="w-full border-t border-gray-50 dark:border-stroke-bold dark:nexus:border-gray-50"
      />
      <li class="flex w-full flex-1 items-center">
        <RouterLink
          to="/dashboard/bulk-add"
          class="sidebar-tile items-center py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-100"
        >
          <i class="fa-solid fa-envelopes-bulk" />
          <span class="text-center nexus:hidden ipad:block">Bulk Add</span>
        </RouterLink>
      </li>
      <li class="flex w-full flex-1 items-center">
        <RouterLink
          to="/dashboard/quick-price"
          class="sidebar-tile items-center py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-100"
        >
          <i class="fa-solid fa-truck-fast" />
          <span class="text-center nexus:hidden ipad:block">Quick Price</span>
        </RouterLink>
      </li>
      <li class="flex w-full flex-1 items-center">
        <div
          class="sidebar-tile pointer-events-none items-center py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-100"
        >
          <div class="relative">
            <span
              class="absolute -top-spacing-150 left-spacing-100 flex max-h-spacing-250 min-w-spacing-250 items-center justify-center rounded-full bg-green-900 p-spacing-50 text-body/m/compact/400"
              >{{ trackers?.newStarts }}</span
            >
            <i class="fa-brands fa-firstdraft" />
          </div>

          <span class="text-center nexus:hidden ipad:block">1st Approach</span>
        </div>
      </li>
      <li class="flex w-full flex-1 items-center">
        <div
          class="sidebar-tile pointer-events-none items-center py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-100"
        >
          <div class="relative">
            <span
              class="absolute -top-spacing-150 left-spacing-100 flex max-h-spacing-250 min-w-spacing-250 items-center justify-center rounded-full bg-violet-900 p-spacing-50 text-body/m/compact/400"
              >{{ trackers?.followUps }}</span
            >
            <i class="fa-solid fa-list-check" />
          </div>
          <span class="text-center nexus:hidden ipad:block">Follow Ups</span>
        </div>
      </li>
      <li class="flex w-full flex-1 items-center">
        <div
          class="sidebar-tile pointer-events-none items-center py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-100"
        >
          <div class="relative">
            <span
              class="absolute -top-spacing-150 left-spacing-100 flex max-h-spacing-250 min-w-spacing-250 items-center justify-center rounded-full bg-sky-800 p-spacing-50 text-body/m/compact/400"
              >{{ trackers?.bulkAdds }}</span
            >
            <i class="fa-solid fa-envelopes-bulk" />
          </div>

          <span class="text-center nexus:hidden ipad:block">Bulk Adds</span>
        </div>
      </li>
      <li v-if="isSuperAdmin" class="flex w-full flex-1 items-center">
        <RouterLink
          to="/dashboard/user-panel"
          class="sidebar-tile items-center py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-100"
        >
          <i class="fa-solid fa-users" />
          <span class="text-center nexus:hidden ipad:block">User Panel</span>
        </RouterLink>
      </li>

      <div
        class="w-full border-t border-gray-50 dark:border-stroke-bold dark:nexus:border-gray-50"
      />
      <li class="flex w-full flex-1 items-center">
        <RouterLink
          to="/profile"
          class="sidebar-tile items-center py-spacing-200 nexus:py-spacing-250 ipad:py-spacing-100"
        >
          <i class="fa fa-user" />
          <span class="text-center nexus:hidden ipad:block">{{
            user?.name
          }}</span>
        </RouterLink>
      </li>
      <li class="flex w-full flex-1 items-center">
        <button
          class="w-full bg-orange-600 py-spacing-200 text-constant-neutral-white hover:bg-orange-700 nexus:py-spacing-250 ipad:py-spacing-100"
          @click="handleLogout()"
        >
          <i class="fa fa-sign-out-alt" />
          <span class="text-center nexus:hidden ipad:block"> Logout </span>
        </button>
      </li>
    </ul>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      open: false,
      isLoading: false,
      showSideBar: false,
      timeout: null,
      isDarkMode: false, // Tracks the current theme
    };
  },
  components: {
    IconX: () => import("@/components/svgIcons/x.vue"),
    IconMoonStar: () => import("@/components/svgIcons/moon-star.vue"),
    IconSun: () => import("@/components/svgIcons/sun.vue"),
  },
  mounted() {
    this.$bus.$on("open-sidebar", () => (this.open = true));
    this.timeout = setTimeout(() => {
      this.showSideBar = true;
      clearTimeout(this.timeout);
    }, 300);
    if (!this.trackers) {
      this.getTrackers();
    }
    // Apply saved theme or system preference
    // if (
    //   localStorage.getItem("theme") === "dark" ||
    //   (!localStorage.getItem("theme") &&
    //     window.matchMedia("(prefers-color-scheme: dark)").matches)
    // ) {
    //   document.documentElement.classList.add("dark");
    //   this.isDarkMode = true;
    // } else {
    //   document.documentElement.classList.remove("dark");
    //   this.isDarkMode = false;
    // }

    if (localStorage.getItem("theme") === "light") {
      document.documentElement.classList.remove("dark");
      this.isDarkMode = false;
    } else {
      // Default to dark mode
      document.documentElement.classList.add("dark");
      this.isDarkMode = true;
    }
  },
  computed: {
    ...mapGetters({
      user: "auth/getUser",
      trackers: "auth/getUserTrackers",
    }),
    isSuperAdmin() {
      return this.user?.role === "SUPER_ADMIN";
    },
  },
  methods: {
    ...mapActions({
      logout: "auth/logout",
      getTrackers: "auth/getTrackers",
    }),
    async handleLogout() {
      this.$bus.$emit("showLoader", {
        showLoader: true,
        loaderText: "Logging out",
      });
      this.isLoading = true;
      const res = await this.logout();
      if (res?.success) {
        this.$router.replace({ path: "/login" });
      }
      this.isLoading = false;
      this.$bus.$emit("showLoader", false);
    },
    closeSideBar() {
      this.open = false;
    },
    toggleTheme() {
      const rootElement = document.documentElement;

      if (rootElement.classList.contains("dark")) {
        rootElement.classList.remove("dark");
        localStorage.setItem("theme", "light"); // Save preference
        this.isDarkMode = false;
      } else {
        rootElement.classList.add("dark");
        localStorage.setItem("theme", "dark"); // Save preference
        this.isDarkMode = true;
      }
    },
  },
  beforeDestroy() {
    clearTimeout(this.timeout);
    this.$bus.$off("open-sidebar");
  },
};
</script>
<style scoped></style>
