import axiosInstance from "@/axiosInstance";
import { buildErrorMessage } from "@/utilities/helpers/utils";

export default {
  state: {
    marketResearchesData: null,
    marketResearchData: null,
    followUpsByCompany: null,
    error: null,
  },
  mutations: {
    SET_MARKET_RESEARCHES_DATA(state, payload) {
      // Preserve the existing companies in the state
      const loadedMarketResearchesList = state.marketResearchesData?.data || [];

      // Merge existing companies with new companies
      const updatedMarketResearchesList = [
        ...loadedMarketResearchesList,
        ...payload.data,
      ];

      // Update the state with the new data, keeping other properties of payload intact
      state.marketResearchesData = {
        ...payload,
        data: updatedMarketResearchesList,
      };
    },
    RESET_MARKET_RESEARCHES_DATA(state) {
      state.marketResearchesData = null;
    },

    SET_MARKET_RESEARCH_DATA(state, payload) {
      state.marketResearchData = payload;
    },
    SET_MARKET_RESEARCH_COMPANIES_AND_FOLLOW_UP_DATA(state, payload) {
      if (state.marketResearchData) {
        state.marketResearchData = {
          ...state.marketResearchData,
          productSellingCompanies: payload,
        };
      }
    },
    SET_FOLLOW_UPS_BY_COMPANY(state, payload) {
      // Preserve the existing follow-ups in the state
      const loadedFollowUps = state.followUpsByCompany?.followUpHistory || [];

      // Merge existing follow-ups with new follow-ups
      const updatedFollowUps = [
        ...loadedFollowUps,
        ...payload?.followUpHistory,
      ];

      // Update the state with the new data, keeping other properties of payload intact
      state.followUpsByCompany = {
        ...payload,
        followUpHistory: updatedFollowUps,
      };
    },

    RESET_FOLLOW_UPS_BY_COMPANY(state) {
      state.followUpsByCompany = null;
    },
    SET_ERROR(state, payload) {
      state.error = payload;
    },
  },
  actions: {
    async getMarketResearches({ commit }, payload) {
      commit("SET_ERROR", null); // Clear previous error
      try {
        const response = await axiosInstance.get(`market-researches`, {
          params: payload?.params,
        });
        if (response?.data?.pagination?.currentPage === 1) {
          commit("RESET_MARKET_RESEARCHES_DATA");
        }
        commit("SET_MARKET_RESEARCHES_DATA", response?.data);
      } catch (error) {
        commit("SET_ERROR", {
          ...error,
          customMessage: "Unable to fetch market researches",
        });
      }
    },
    async getMarketResearchDetails({ commit }, payload) {
      commit("SET_ERROR", null); // Clear previous error
      commit("SET_MARKET_RESEARCH_DATA", null); // Clear previous data
      try {
        const response = await axiosInstance.get(
          `market-researches/${payload?.id}`,
          {
            params: payload?.params,
          },
        );

        commit("SET_MARKET_RESEARCH_DATA", response?.data);

        // Get category IDs if available
        const productIds = response?.data?.products?.map((item) => item?._id);
        if (productIds && productIds.length > 0) {
          const response = await axiosInstance.post(`companies/by-products`, {
            productIds,
            marketResearchId: payload?.id,
          });
          commit(
            "SET_MARKET_RESEARCH_COMPANIES_AND_FOLLOW_UP_DATA",
            response?.data?.data,
          );
        }
      } catch (error) {
        commit("SET_ERROR", {
          ...error,
          customMessage: "Unable to fetch market research data",
        });
      }
    },

    async deleteMarketResearch({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.delete(
          `market-researches/${payload?.id}`,
        );
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Unable to delete market research",
        };
        commit("SET_ERROR", errorPayload);
        return {
          message: buildErrorMessage(errorPayload),
          success: false,
        };
      }
    },

    async getAllFollowUpsByCompany({ commit }, payload) {
      commit("SET_ERROR", null); // Clear previous error
      try {
        const response = await axiosInstance.get(`follow-up`, {
          params: payload?.params,
        });
        if (response?.data?.currentPage === 1) {
          commit("RESET_FOLLOW_UPS_BY_COMPANY");
        }
        commit("SET_FOLLOW_UPS_BY_COMPANY", response?.data);
      } catch (error) {
        commit("SET_ERROR", {
          ...error,
          customMessage: "Unable to fetch follow-ups history",
        });
      }
    },
    async addMarketResearch({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.post(
          `market-researches`,
          payload,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          },
        );
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Unable to create new market research",
        };
        commit("SET_ERROR", errorPayload);
        return { message: buildErrorMessage(errorPayload), success: false };
      }
    },
    async updateMarketResearch({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.patch(
          `market-researches/${payload?.id}`,
          payload?.body,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          },
        );
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Unable to update market research",
        };
        commit("SET_ERROR", errorPayload);
        return { message: buildErrorMessage(errorPayload), success: false };
      }
    },
    async addQuotation({ commit }, payload) {
      commit("SET_ERROR", null);
      try {
        const response = await axiosInstance.post(
          `quotations/follow-up-quotation`,
          payload,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          },
        );
        return response?.data;
      } catch (error) {
        let errorPayload = {
          ...error,
          customMessage: "Unable to add Follow Up",
        };
        commit("SET_ERROR", errorPayload);
        return {
          success: false,
          message: buildErrorMessage(errorPayload),
        };
      }
    },
  },
  getters: {
    getErrorMessage: (state) => buildErrorMessage(state.error),
  },
  namespaced: true,
};
