<template>
  <div class="flex h-full w-full flex-col bg-bg-neutral-default">
    <div
      class="mx-spacing-200 my-spacing-300 flex max-w-[800px] items-center rounded-08 border-4 border-bg-neutral-sunken bg-bg-neutral-sunken px-spacing-0 py-spacing-0 text-body/l/relaxed/400 shadow-inner dark:border-bg-neutral-sunken"
    >
      <div
        v-for="(tab, index) in tabs"
        :key="index"
        :class="[
          'flex-1 whitespace-nowrap rounded-06 px-spacing-150 py-spacing-100 text-center',
          tab.isActive
            ? 'bg-bg-neutral-default text-content-on-neutral-primary dark:bg-bg-neutral-raised'
            : 'cursor-pointer text-content-on-neutral-tertiary',
        ]"
        @click="selectTab(index)"
      >
        {{ tab.label }}
      </div>
    </div>

    <div
      v-if="tabs[0]?.isActive"
      class="mb-spacing-250 flex flex-1 flex-col overflow-y-auto"
    >
      <div class="mx-spacing-200 flex max-w-[800px] flex-col">
        <div class="flex flex-col gap-spacing-200 py-spacing-50 ipad:flex-row">
          <FormInput
            ref="date"
            label="Date"
            v-model="formData.date"
            inputId="date"
            :input-type="'date'"
            @enter-pressed="$focusNextElement('originList')"
            :field-validations="fieldValidations?.date"
            :order="1"
          />
          <FormInput
            ref="originList"
            label="Origin List"
            v-model="formData.originList"
            inputId="originList"
            @enter-pressed="$focusNextElement('location')"
            :field-validations="fieldValidations?.originList"
            :order="2"
          />
        </div>
        <div class="flex flex-col gap-spacing-200 ipad:flex-row">
          <!-- <FormInput
            ref="country"
            label="Country"
            v-model="formData.country"
            inputId="country"
            @enter-pressed="$focusNextElement('category')"
            :field-validations="fieldValidations?.country"
            :order="3"
          /> -->
          <LocationSearchDropdown
            :dropdown-label="'Location'"
            :dropdown-id="'location'"
            :selected-location="
              formData?.location ? formData?.location : undefined
            "
            @on-location-update="updateLocation"
            @enter-pressed="$focusNextElement('products')"
            :order="3"
            class="flex-1"
          />
          <!-- <FormInput
          ref="category"
          label="Category"
          v-model="formData.category"
          inputId="category"
          @enter-pressed="$focusNextElement('addingUser')"
          :field-validations="fieldValidations?.probableCategory"
          :order="4"
          class="flex-1"
          /> -->
        </div>
        <ProductSearchDropdown
          :dropdown-label="'Products'"
          :dropdown-id="'products'"
          :products="formData.products"
          :is-multi-select="true"
          class="my-spacing-100"
          @on-products-update="updateProducts"
          :order="4"
        />
        <!-- <UserSearchDropDown
          dropdown-label="Adding User"
          dropdown-id="addingUser"
          :group="formData.addingUser"
          :is-multi-select="true"
          @on-group-update="handleGroupUpdate"
          class="mb-spacing-100"
        /> -->
      </div>
      <SpreadSheet
        class="sticky top-0 flex-1 px-spacing-200"
        @spreadsheet-data="handleSave"
      />
    </div>
    <div
      v-else-if="tabs[1]?.isActive"
      class="mb-spacing-250 flex flex-1 flex-col overflow-y-auto"
    >
      <ClassicTable
        :headers="companyHeaders"
        v-if="bulk_Added_Companies?.length > 0"
      >
        <template #table-body>
          <tr
            v-for="(company, index) in bulk_Added_Companies"
            :key="index"
            class="cursor-pointer border-b border-stroke-medium text-body/m/relaxed/400 text-content-on-neutral-primary"
            @click="handleEdit(company)"
          >
            <td class="py-spacing-150 pr-spacing-100">
              {{ company?.companyName }}
            </td>
            <td class="py-spacing-150 pr-spacing-100">
              {{ company?.email }}
            </td>
            <td class="py-spacing-150 pr-spacing-100">
              {{ company?.genNumber }}
            </td>
            <td class="py-spacing-150 pr-spacing-100">
              {{ company?.commNumber }}
            </td>
            <td class="py-spacing-150 pr-spacing-100">
              {{ company?.location }}
            </td>
            <td class="py-spacing-150 pr-spacing-100">
              {{ company?.originList }}
            </td>
            <td class="py-spacing-150 pr-spacing-100">
              <span
                v-if="company?.addedToMainDB"
                :class="getArchiveReasonClass('YES')"
                class="rounded-08 px-spacing-100 py-spacing-50"
                >YES</span
              >
              <span
                v-else
                :class="getArchiveReasonClass('NO')"
                class="rounded-08 px-spacing-100 py-spacing-50"
                >NO</span
              >
            </td>
          </tr>
        </template>
      </ClassicTable>
    </div>
    <div
      v-else-if="tabs[2]?.isActive"
      class="mb-spacing-250 flex flex-1 flex-col overflow-y-auto"
    >
      <ClassicTable
        :headers="archiveHeaders"
        v-if="archivedCompanies?.length > 0"
      >
        <template #table-body>
          <tr
            v-for="(company, index) in archivedCompanies"
            :key="index"
            class="border-b border-stroke-medium text-body/m/relaxed/400 text-content-on-neutral-primary"
          >
            <td class="py-spacing-150 pr-spacing-100">
              {{ company?.addUser?.name }}
            </td>
            <td class="py-spacing-150 pr-spacing-100">
              <span
                :class="getArchiveReasonClass(company?.archiveReason)"
                class="rounded-08 px-spacing-100 py-spacing-50"
                >{{ company?.archiveReason }}</span
              >
            </td>
            <td class="py-spacing-150 pr-spacing-100">
              {{ company?.companyName }}
            </td>
            <td class="py-spacing-150 pr-spacing-100">
              {{ company?.genNumber }}
            </td>
            <td class="py-spacing-150 pr-spacing-100">
              {{ company?.commNumber }}
            </td>
            <td class="py-spacing-150 pr-spacing-100">
              {{ company?.location }}
            </td>
            <td class="py-spacing-150 pr-spacing-100">
              {{ company?.originList }}
            </td>
          </tr>
        </template>
      </ClassicTable>
    </div>
    <AddNewCompanyModal />
  </div>
</template>

<script>
import { FormValidationRule } from "@/utilities/helpers/formValidations";
import { getDateFromToday } from "@/utilities/helpers/utils";
import { mapActions, mapGetters } from "vuex";
import axiosInstance from "@/axiosInstance";
export default {
  components: {
    FormInput: () => import("@/components/General/FormInput.vue"),
    SpreadSheet: () => import("@/components/BulkAdd/SpreadSheet.vue"),
    // UserSearchDropDown: () =>
    //   import("@/components/Utility/CustomDropdowns/UserSearch.vue"),
    // GeneralButton: () => import("@/components/General/CustomButton.vue"),
    ClassicTable: () => import("@/components/General/Table/ClassicTable.vue"),
    ProductSearchDropdown: () =>
      import("@/components/Utility/CustomDropdowns/ProductSearch.vue"),
    LocationSearchDropdown: () =>
      import("@/components/Utility/CustomDropdowns/LocationSearch.vue"),
    AddNewCompanyModal: () => import("@/components/Modals/AddNewCompany.vue"),
  },

  data() {
    return {
      formData: {
        date: getDateFromToday(0),
        originList: "",
        location: null,
        products: [],
        // addingUser: [],
      },
      fieldValidations: {
        date: new FormValidationRule({}),
        originList: new FormValidationRule({}),
        location: new FormValidationRule({}),
        products: new FormValidationRule({}),
      },
      archiveHeaders: [
        { key: "user", value: "User" },
        { key: "status", value: "Status" },
        { key: "name", value: "Name" },
        { key: "genNumber", value: "General Number" },
        { key: "commNumber", value: "Commercial Number" },
        { key: "country", value: "Country" },
        { key: "originList", value: "Origin List" },
      ],
      companyHeaders: [
        { key: "name", value: "Name" },
        { key: "email", value: "Email" },
        { key: "genNumber", value: "General Number" },
        { key: "commNumber", value: "Commercial Number" },
        { key: "country", value: "Country" },
        { key: "originList", value: "Origin List" },
        { key: "addedToMainDB", value: "Added to main DB" },
      ],
      isFormSubmitting: false,
      bulk_Added_Companies: [],
      archivedCompanies: [],
      tabs: [
        { label: "Bulk Add", isActive: true },
        { label: "Companies List", isActive: false },
        { label: "Archive", isActive: false },
      ],
    };
  },
  computed: {
    ...mapGetters({
      user: "auth/getUser",
    }),
  },
  mounted() {
    this.$bus.$on("refetch-bulk-added-companies", () => {
      this.showAllCompanies();
    });
    this.$bus.$on("refetch-archived-companies", () => {
      this.showArchived();
    });
  },
  methods: {
    ...mapActions({
      addCompanies: "company/addCompanies",
      getTrackers: "auth/getTrackers",
    }),

    selectTab(index) {
      this.tabs.forEach((tab, i) => {
        tab.isActive = i === index;
      });
      if (this.tabs[1]?.isActive) {
        this.showAllCompanies();
      } else if (this.tabs[2].isActive) {
        this.showArchived();
      }
    },

    getArchiveReasonClass(archiveReason) {
      switch (archiveReason) {
        case "NO_INTERNET_REFRENCE":
          return "bg-bg-status-info text-content-on-status-on-info";
        case "NO":
          return "bg-bg-status-error text-content-on-status-on-error";
        case "YES":
          return "bg-bg-status-success text-content-on-status-on-success";
        case "COMPANY_SUCCESSFULLY_ADDED":
          return "bg-bg-status-success text-content-on-status-on-success";
        case "DELETED_FROM_MAIN_DB":
          return "bg-bg-status-error text-content-on-status-on-error";
        default:
          return "bg-bg-status-warning text-content-on-status-on-warning";
      }
    },

    updateLocation(location) {
      this.formData.location = location;
    },
    updateProducts(products) {
      this.formData.products = products;
    },
    async handleSave(spreadsheetData) {
      if (spreadsheetData?.length < 1) {
        return this.$toast.error("Please add one or more companies");
      }
      const isValid = this.$validateForm();
      if (!isValid) {
        return;
      }
      this.isFormSubmitting = true;
      const payload = this.buildPayload(spreadsheetData);
      const res = await this.addCompanies(payload);
      if (res?.success) {
        this.$toast.success(res?.message);
        this.getTrackers();
        this.resetAllFields();
      } else {
        this.$toast.error(res?.message);
      }
      this.isFormSubmitting = false;
    },
    buildPayload(spreadsheetData) {
      let payload = spreadsheetData?.map((item) => {
        return {
          companyName: item?.companyName,
          genNumber:
            item?.generalNumber === "+0000 000 00 00 000"
              ? ""
              : item?.generalNumber,
          commNumber:
            item?.commercialNumber === "+0000 000 00 00 000"
              ? ""
              : item?.commercialNumber,
          email: item?.email,
          annualRevenue: Number(item?.annualRevenue),
          location: this.formData?.location?.display_name || "",
          lat: this.formData?.location?.lat || "",
          long: this.formData?.location?.lon || "",
          origin: "BULK_ADD",
          originList: this.formData?.originList,
          agents: [],
          products: this.formData?.products?.map((item) => ({
            productId: item?._id,
          })),
          addUser: this.user?._id,
          addToArchive: true,
          addToMainDB: item?.addToMainDB || false,
        };
      });
      return payload;
    },
    async showAllCompanies() {
      this.$bus.$emit("showLoader", { showLoader: true });
      const res = await axiosInstance.get(
        "companies/archive-confirmation-pending",
      );
      if (res?.data?.success) {
        this.bulk_Added_Companies = res?.data?.data;
      } else {
        this.$toast.error(res?.data?.message);
      }
      this.$bus.$emit("showLoader", false);
    },
    resetAllFields() {
      this.formData.date = "";
      this.formData.location = null;
      this.formData.originList = "";
      this.formData.products = [];
      this.$bus.$emit("clear-spreadsheet-data");
    },
    handleEdit(company) {
      this.$bus.$emit("add-new-company-modal", {
        showModal: true,
        companyId: company?._id,
        formData: {
          companyName: company?.companyName,
          genNumber: company?.genNumber,
          commNumber: company?.commNumber,
          email: company?.email,
          annualRevenue: company?.annualRevenue,

          // company origin details
          origin: company?.origin,
          originList: company?.originList,

          // location
          location: {
            display_name: company?.location,
            lat: company?.lat,
            lon: company?.long,
          },

          GS: company?.GS,
          RS: company?.RS,

          agents: company?.agents?.map((item) => ({
            genNumber: item?.genNumber,
            commNumber: item?.commNumber,
            email: item?.email,
          })),

          products: company?.products?.map((item) => ({
            originalTitle: item?.productId?.title,
            _id: item?.productId?._id,
          })),
        },
        analyze: this.tabs[2]?.isActive ? false : true,
        addedToMainDB: company?.addedToMainDB || false,
        update: true,
      });
    },
    async showArchived() {
      this.$bus.$emit("showLoader", { showLoader: true });

      const res = await axiosInstance.get("companies/archived");
      if (res?.data?.success) {
        this.archivedCompanies = res?.data?.data;
      } else {
        this.$toast.error(res?.data?.message);
      }
      this.$bus.$emit("showLoader", false);
    },
  },
  beforeDestroy() {
    this.$bus.$off("refetch-bulk-added-companies");
    this.$bus.$off("refetch-archived-companies");
  },
};
</script>
