<template>
  <div class="h-full">
    <div class="flex h-full min-h-full flex-col overflow-y-auto">
      <div class="flex-1">
        <div class="px-spacing-200 pt-spacing-300 ipad:px-spacing-300">
          <div
            class="flex flex-col items-start justify-between gap-spacing-200 ipadPro:flex-row"
          >
            <!-- Left Section: Title and Best Prices -->
            <div class="w-full ipadPro:w-auto">
              <div
                class="mb-spacing-150 flex flex-col gap-spacing-150 ipadPro:flex-row ipadPro:items-center"
              >
                <h2
                  class="text-headline/s/relaxed/500 text-constant-brand-primary ipad:text-headline/m/relaxed/500"
                >
                  {{ marketResearchData?.name }}
                </h2>
                <div class="flex items-center gap-spacing-150">
                  <div @click="handleEdit()" v-if="marketResearchData?.name">
                    <GeneralButton
                      :button-type="'button'"
                      :type="'brand-secondary'"
                      :size="'small'"
                      :emphasis="'secondary'"
                      :icon-position="'none'"
                      :icon="''"
                      :image="''"
                      :is-disabled="false"
                      :is-loading="false"
                      :label="'Edit Details'"
                    />
                  </div>
                  <div
                    @click="viewGuidelinesAndEmailTemplate()"
                    v-if="marketResearchData?.name"
                  >
                    <GeneralButton
                      :button-type="'button'"
                      :type="'brand'"
                      :size="'small'"
                      :emphasis="'secondary'"
                      :icon-position="'none'"
                      :icon="''"
                      :image="''"
                      :is-disabled="
                        !marketResearchData?.guidelines &&
                        !marketResearchData?.emailTemplate
                      "
                      :is-loading="false"
                      :label="'Guidelines & Email Template'"
                    />
                  </div>
                  <div
                    @click="viewAttachedFiles()"
                    v-if="marketResearchData?.name"
                  >
                    <GeneralButton
                      :button-type="'button'"
                      :type="'success'"
                      :size="'small'"
                      :emphasis="'secondary'"
                      :icon-position="'none'"
                      :icon="''"
                      :image="''"
                      :is-disabled="
                        marketResearchData?.relatedFiles?.length < 1
                      "
                      :is-loading="false"
                      :label="'Attached Files'"
                    />
                  </div>
                </div>
              </div>
              <h3
                class="border-b border-stroke-bold pb-spacing-50 text-overline/l/relaxed/500 text-content-on-neutral-primary"
              >
                BEST PRICES
              </h3>
              <ClassicTable
                class="mt-spacing-100 text-body/l/relaxed/400 text-content-on-neutral-primary"
                :headers="bestPriceHeaders"
                :horizontal-padding="'px-spacing-0'"
                :bgClass="'bg-transparent'"
              >
                <template #table-body>
                  <tr
                    v-for="(item, index) in bestPrices"
                    :key="index"
                    class="border-b border-stroke-medium"
                  >
                    <td class="py-spacing-50 pr-spacing-150">
                      <p>{{ item?.productName }}</p>
                      <span
                        v-if="item?.variable"
                        class="text-body/m/relaxed/400 italic text-content-on-neutral-secondary"
                      >
                        [{{ item?.variable?.name }},
                        {{ item?.variable?.coefficient }}]</span
                      >
                      <span
                        v-else
                        class="text-body/m/relaxed/400 italic text-content-on-neutral-secondary"
                      >
                        [default, 1]</span
                      >
                    </td>
                    <td
                      v-if="item?.companyName"
                      class="py-spacing-50 pr-spacing-150"
                    >
                      {{ item?.companyName }}
                    </td>
                    <td
                      v-else
                      class="py-spacing-50 pr-spacing-150 text-content-on-neutral-tertiary"
                    >
                      Not Available
                    </td>
                    <td
                      v-if="!isNaN(parseInt(item?.price))"
                      class="py-spacing-50 pr-spacing-150"
                    >
                      {{ item?.price | toCurrency }}
                    </td>
                    <td
                      v-else
                      class="py-spacing-50 pr-spacing-150 text-content-on-neutral-tertiary"
                    >
                      Not Available
                    </td>
                    <td
                      v-if="!isNaN(parseInt(item?.price))"
                      class="py-spacing-50 pr-spacing-150 text-content-on-neutral-secondary"
                    >
                      {{ item?.productUnit || "UNIT" }}
                    </td>
                    <td
                      v-else
                      class="py-spacing-50 pr-spacing-150 text-content-on-neutral-tertiary"
                    >
                      Not Available
                    </td>
                  </tr>
                </template>
              </ClassicTable>
            </div>

            <!-- Right Section: Scoring -->
            <div class="w-full text-content-on-neutral-primary ipadPro:w-auto">
              <h3
                class="border-b border-stroke-bold pb-spacing-50 text-overline/l/relaxed/500 text-content-on-neutral-primary"
              >
                SCORING
              </h3>
              <div class="hidden gap-spacing-200 nexus:flex">
                <ClassicTable
                  :headers="userScoringHeader"
                  :horizontal-padding="'px-spacing-0'"
                  class="mt-spacing-100"
                  :bgClass="'bg-transparent'"
                >
                  <template #table-body>
                    <tr
                      v-for="(score, index) in scoring?.slice(
                        0,
                        scoring?.length / 2 + 1,
                      )"
                      :key="index"
                      class="text-body/l/relaxed/400 text-content-on-neutral-primary"
                    >
                      <td class="pr-spacing-100">{{ score.name }}</td>
                      <td class="text-body/l/relaxed/500">
                        {{ score.points | toPercentage(true) }}
                      </td>
                    </tr>
                  </template>
                </ClassicTable>
                <ClassicTable
                  :headers="userScoringHeader"
                  :horizontal-padding="'px-spacing-0'"
                  class="mt-spacing-100"
                  :bgClass="'bg-transparent'"
                >
                  <template #table-body>
                    <tr
                      v-for="(score, index) in scoring?.slice(
                        scoring?.length / 2 + 1,
                      )"
                      :key="index"
                      class="text-body/l/relaxed/400 text-content-on-neutral-primary"
                    >
                      <td class="pr-spacing-100">{{ score.name }}</td>
                      <td class="text-body/l/relaxed/500">
                        {{ score.points | toPercentage(true) }}
                      </td>
                    </tr>
                  </template>
                </ClassicTable>
              </div>
              <div class="nexus:hidden">
                <ClassicTable
                  :headers="userScoringHeader"
                  :horizontal-padding="'px-spacing-0'"
                  class="mt-spacing-100"
                  :bgClass="'bg-transparent'"
                >
                  <template #table-body>
                    <tr
                      v-for="(score, index) in scoring"
                      :key="index"
                      class="text-body/l/relaxed/400 text-content-on-neutral-primary"
                    >
                      <td class="pr-spacing-100">{{ score.name }}</td>
                      <td class="text-body/l/relaxed/500">
                        {{ score.points | toPercentage(true) }}
                      </td>
                    </tr>
                  </template>
                </ClassicTable>
              </div>
            </div>
          </div>
        </div>
        <div
          class="top-spacing-0 z-1 mt-spacing-100 flex flex-col gap-spacing-200 bg-bg-neutral-default px-spacing-200 py-spacing-200 ipad:sticky ipad:px-spacing-300 ipadPro:flex-row ipadPro:items-center"
        >
          <div class="flex-1">
            <Search
              placeholder="Search..."
              @search="search"
              label="Company Name"
              search-id="company-name-search"
            />
          </div>
          <div
            class="flex flex-1 flex-col gap-spacing-200 nexus:flex-row nexus:items-center ipadPro:flex-none"
          >
            <div
              class="flex flex-1 cursor-pointer flex-nowrap items-center overflow-hidden rounded-08 bg-bg-neutral-raised px-spacing-200 py-spacing-150 ipadPro:flex-none"
              :class="
                !sortByFollowUpDate
                  ? 'bg-bg-neutral-raised'
                  : 'bg-bg-status-warning'
              "
              @click="handleSort()"
            >
              <p
                class="w-full whitespace-nowrap text-center text-body/m/relaxed/400"
                :class="
                  sortByFollowUpDate
                    ? 'text-content-on-status-on-warning'
                    : 'text-content-on-neutral-secondary'
                "
              >
                Sort By: Follow up date
              </p>
            </div>

            <div
              class="flex flex-1 items-center overflow-hidden rounded-08 bg-bg-neutral-raised"
            >
              <div
                v-for="(tab, index) in tabs"
                :key="index"
                class="w-1/2 min-w-spacing-1600 px-spacing-200 py-spacing-150"
                :class="
                  selectedTab?.id === tab?.id
                    ? 'bg-bg-brand-secondary'
                    : 'cursor-pointer'
                "
                tabindex="0"
                @click="
                  selectedTab?.id === tab?.id ? undefined : (selectedTab = tab)
                "
              >
                <p
                  class="whitespace-nowrap text-center text-body/m/relaxed/400"
                  :class="
                    selectedTab?.id === tab?.id
                      ? 'text-content-on-brand-secondary'
                      : 'text-content-on-neutral-secondary'
                  "
                >
                  {{ tab?.name }}
                </p>
              </div>
            </div>
          </div>
        </div>

        <div v-if="isFullView">
          <FollowUpFullView
            v-for="(
              companyData, index
            ) in sortedAndFilteredProductSellingCompanies"
            :key="index"
            :market-research-data="marketResearchData"
            :company-data="companyData"
            :search-query="searchQuery"
          />
        </div>
        <div v-else>
          <ClassicTable
            :headers="compactViewHeaders"
            :bgClass="'bg-transparent'"
          >
            <template #table-body>
              <tr
                v-for="(
                  companyData, index
                ) in sortedAndFilteredProductSellingCompanies"
                :key="index"
                @click="showFullViewForFollowUp(companyData)"
                class="cursor-pointer border-b border-stroke-medium text-left text-body/m/relaxed/400 text-content-on-neutral-primary nexus:text-body/l/relaxed/400"
              >
                <td
                  v-if="companyData?.followUpHistory?.status"
                  class="py-spacing-100 pr-spacing-100"
                >
                  {{ companyData?.followUpHistory?.status }}
                </td>
                <td
                  v-else
                  class="py-spacing-100 pr-spacing-100 text-content-on-neutral-tertiary"
                >
                  Not Available
                </td>
                <td
                  v-if="companyData?.followUpHistory?.nextFollowUpDate"
                  class="py-spacing-100 pr-spacing-100"
                >
                  {{ companyData?.followUpHistory?.nextFollowUpDate | toDate }}
                </td>
                <td
                  v-else
                  class="py-spacing-100 pr-spacing-100 text-content-on-neutral-tertiary"
                >
                  Not Available
                </td>
                <td
                  v-if="
                    companyData?.followUpHistory?.recentFollowUps?.length > 0
                  "
                  class="py-spacing-100 pr-spacing-100"
                >
                  {{
                    companyData?.followUpHistory?.recentFollowUps[0]?.date
                      | toDate
                  }}
                  |
                  {{
                    companyData?.followUpHistory?.recentFollowUps[0]?.note ||
                    "------------"
                  }}
                </td>
                <td
                  v-else
                  class="py-spacing-100 pr-spacing-100 text-content-on-neutral-tertiary"
                >
                  Not Available
                </td>
                <td class="py-spacing-100 pr-spacing-100">
                  <p
                    @click.stop="goToCompanyDetailsPage(companyData?.companyId)"
                    class="cursor-pointer hover:text-hover-constant-brand-primary"
                    v-html="
                      $highlightMatch(companyData?.companyName, searchQuery)
                    "
                  ></p>
                </td>
                <td class="py-spacing-100 pr-spacing-100">
                  <div @click.stop="startFollowUp(companyData)">
                    <GeneralButton
                      :button-type="'button'"
                      :type="
                        companyData?.followUpHistory?.recentFollowUps
                          ?.length === 0
                          ? 'brand'
                          : 'success'
                      "
                      :size="'small'"
                      :emphasis="'secondary'"
                      :icon-position="'none'"
                      :label="
                        companyData?.followUpHistory?.recentFollowUps
                          ?.length === 0
                          ? 'Start'
                          : 'Add'
                      "
                    />
                  </div>
                </td>
              </tr>
            </template>
          </ClassicTable>
        </div>
      </div>
      <div
        class="sticky bottom-0 flex gap-spacing-200 bg-bg-neutral-default px-spacing-200 py-spacing-200 ipad:px-spacing-300"
      >
        <GeneralButton
          :button-type="'button'"
          :type="'brand'"
          :size="'small'"
          :emphasis="'secondary'"
          :icon-position="'none'"
          :label="'New Company'"
          @click.native="addNewCompany"
        />
        <div @click="handleDelete()">
          <GeneralButton
            :button-type="'button'"
            :type="'destructive'"
            :size="'small'"
            :emphasis="'secondary'"
            :icon-position="'none'"
            :icon="''"
            :image="''"
            :is-disabled="false"
            :is-loading="false"
            :label="'Delete Market Research'"
          />
        </div>
      </div>
    </div>
    <AddNewCompanyModal />
    <AddDetailsLaterModal />
    <FollowUpFullViewModal />
    <AddNewMarketResearchModal />
    <ResearchStatusModal />
    <MoreFollowUpsModal />
    <GuidelinesAndEmailTemplateModal />
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  components: {
    ClassicTable: () => import("@/components/General/Table/ClassicTable.vue"),
    ResearchStatusModal: () =>
      import("@/components/Modals/ResearchStatusModal.vue"),
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
    MoreFollowUpsModal: () =>
      import("@/components/Modals/MoreFollowUpsModal.vue"),
    Search: () => import("@/components/General/Search.vue"),
    AddNewMarketResearchModal: () =>
      import("@/components/Modals/AddNewMarketResearch.vue"),
    AddNewCompanyModal: () => import("@/components/Modals/AddNewCompany.vue"),
    FollowUpFullView: () =>
      import("@/components/MarketResearch/FollowUpFullView.vue"),
    FollowUpFullViewModal: () =>
      import("@/components/Modals/FollowUpFullViewModal.vue"),
    AddDetailsLaterModal: () =>
      import("@/components/Modals/AddDetailsLaterModal.vue"),
    GuidelinesAndEmailTemplateModal: () =>
      import("@/components/Modals/GuidelinesAndEmailTemplateModal.vue"),
  },
  data() {
    return {
      bestPriceHeaders: [
        { key: "productName", value: "Product Name", sortable: false },
        { key: "companyName", value: "Company Name", sortable: false },
        { key: "price", value: "Price", sortable: false },
        { key: "unit", value: "Unit", sortable: false },
      ],
      tabs: [
        { name: "Compact View", id: "compact" },
        { name: "Full View", id: "full" },
      ],
      compactViewHeaders: [
        { key: "status", value: "Status", sortable: false },
        { key: "followUpDate", value: "Follow Up Date", sortable: false },
        { key: "lastUpdate", value: "Last Update", sortable: false },
        { key: "companyName", value: "Company Name", sortable: false },
        { key: "action", value: "Action", sortable: false },
      ],
      selectedTab: { name: "COMPACT VIEW", id: "compact" },
      userScoringHeader: [
        { key: "User", value: "User", sortable: false },
        { key: "Score", value: "Score", sortable: false },
      ],
      isLoading: false,
      sortByFollowUpDate: false,
      searchQuery: "",
      deleteHandler: null,
      refetchHandler: null,
    };
  },
  mounted() {
    this.deleteHandler = async ({ id }) => {
      if (this.marketResearchData?._id === id) {
        let payload = { id };
        const res = await this.deleteMarketResearch(payload);
        if (res?.success) {
          this.$toast.success(res?.message);
          this.$router.push("/dashboard/market-researches");
        } else {
          this.$toast.error(res?.message);
        }
      }
    };
    this.refetchHandler = () => {
      const marketResearchId = this.$route?.params?.id;
      this.fetchMarketResearchDetails(marketResearchId);
    };
    this.$bus.$on("delete-market-research", this.deleteHandler);
    this.$bus.$on("refetch-market-research-details", this.refetchHandler);
    const marketResearchId = this.$route?.params?.id;
    this.fetchMarketResearchDetails(marketResearchId);
  },
  computed: {
    ...mapState({
      marketResearchData: (state) => state.marketResearch?.marketResearchData,
    }),

    sortedAndFilteredProductSellingCompanies() {
      let output = [];

      if (this.marketResearchData?.productSellingCompanies?.data?.length > 0) {
        output = [...this.marketResearchData.productSellingCompanies.data];

        // Apply search filter
        if (this.searchQuery) {
          output = output.filter((companyData) =>
            companyData?.companyName
              ?.toLowerCase()
              ?.includes(this.searchQuery?.trim()?.toLowerCase()),
          );
        }

        // Sort by nextFollowUpDate (nearest first, nulls last)
        if (this.sortByFollowUpDate) {
          output = output?.sort((a, b) => {
            const dateA = a?.followUpHistory?.nextFollowUpDate
              ? new Date(a.followUpHistory.nextFollowUpDate)
              : null;
            const dateB = b?.followUpHistory?.nextFollowUpDate
              ? new Date(b.followUpHistory.nextFollowUpDate)
              : null;

            if (!dateA && !dateB) return 0; // Both null
            if (!dateA) return 1; // `a` is null, put it after `b`
            if (!dateB) return -1; // `b` is null, put it after `a`

            return dateA - dateB; // Nearest date first
          });
        }
      }

      return output;
    },
    isFullView() {
      return this.selectedTab.id === "full";
    },

    bestPrices() {
      let output = [];
      if (this.marketResearchData?.productSellingCompanies?.minPricesArray) {
        output =
          this.marketResearchData?.productSellingCompanies?.minPricesArray;
      }
      return output;
    },

    scoring() {
      let output = this.marketResearchData?.group?.map((item) => ({
        name: item.name,
        points: 20,
      }));
      return output;
    },
  },
  methods: {
    ...mapActions("marketResearch", [
      "getMarketResearchDetails",
      "deleteMarketResearch",
    ]),

    async handleDelete() {
      this.$bus.$emit("remove-confirmation-modal", {
        showModal: true,
        confirmationText: `This market research (${this.marketResearchData?.name}) and all of its product prices and quotations will be deleted pemanently.`,
        confirmEvent: "delete-market-research",
        id: this.marketResearchData?._id,
      });
    },

    addNewCompany() {
      this.$bus.$emit("add-new-company-modal", {
        showModal: true,
        formData: {
          products: this.marketResearchData?.products?.map((item) => ({
            originalTitle: item?.title,
            _id: item?._id,
          })),
        },
        default: true,
        refreshFlowEvent: "refetch-market-research-details",
      });
    },

    viewGuidelinesAndEmailTemplate() {
      this.$bus.$emit("guidelines-and-email-template-modal", {
        showModal: true,
        marketResearchData: this.marketResearchData,
      });
    },

    viewAttachedFiles() {
      this.$bus.$emit("guidelines-and-email-template-modal", {
        showModal: true,
        marketResearchData: this.marketResearchData,
        type: "ATTACHED_FILES",
      });
    },

    handleEdit() {
      this.$bus.$emit("add-new-market-research-modal", {
        showModal: true,
        marketResearchData: this.marketResearchData,
        update: true,
      });
    },

    handleSort() {
      this.sortByFollowUpDate = !this.sortByFollowUpDate;
    },

    async fetchMarketResearchDetails(marketResearchId) {
      this.isLoading = true;
      this.$bus.$emit("showLoader", {
        showLoader: true,
      });
      await this.getMarketResearchDetails({ id: marketResearchId });

      const companyId = this.$route?.query?.companyId;
      const productId = this.$route?.query?.productId;

      this.$bus.$emit("showLoader", false);
      this.isLoading = false;

      if (companyId) {
        this.selectedTab = { name: "Full View", id: "full" };

        this.$nextTick(() => {
          requestAnimationFrame(() => {
            if (productId) {
              this.scrollToElement(`company-${companyId}-product-${productId}`);
            } else {
              this.scrollToElement(`company-${companyId}`);
            }
          });
        });
      }
    },

    async scrollToElement(elementId) {
      this.$nextTick(() => {
        requestAnimationFrame(() => {
          requestAnimationFrame(() => {
            let element = document.getElementById(elementId);

            if (element) {
              element.scrollIntoView({
                behavior: "smooth",
                block: "center",
              });
              element.classList.add("highlight");
            }
          });
        });
      });
    },

    // compact view
    openStatusUpdateModal(companyData) {
      this.$bus.$emit("research-status-modal", {
        showModal: true,
        companyData,
        marketResearchId: this.marketResearchData?._id,
        marketResearchSettings: {
          ...this.marketResearchData?.settings,
          dateStart: this.marketResearchData?.dateStart,
        },
        statusOptions: this.marketResearchData?.statusOptions,
      });
    },

    // compact view
    startFollowUp(companyData) {
      this.openStatusUpdateModal(companyData);
    },
    search(query) {
      this.searchQuery = query;
    },
    showFullViewForFollowUp(companyData) {
      this.$bus.$emit("follow-up-full-view-modal", {
        showModal: true,
        companyData,
        marketResearchData: this.marketResearchData,
      });
    },

    goToCompanyDetailsPage(companyId) {
      this.$router.push(`/dashboard/companies/${companyId}`);
    },
  },
  beforeDestroy() {
    this.$bus.$off("refetch-market-research-details", this.refetchHandler);
    this.$bus.$off("delete-market-research", this.deleteHandler);
  },
};
</script>
