import moment from "moment";
import Vue from "vue";

Vue.filter("toDate", function (value, dateFormat = "DD-MM-YYYY") {
  if (!value) return "";
  return moment(value)?.format(dateFormat);
});

Vue.filter("toPercentage", function (value, formattingOnly) {
  if (typeof value !== "number" || isNaN(value)) {
    return value; // Return the original value if it's not a number
  }
  return formattingOnly
    ? `${value.toFixed(2)}%`
    : `${(value * 100).toFixed(2)}%`; // Convert to percentage with 2 decimal places
});

Vue.filter("toDistance", function (value, unit = "km") {
  if (typeof value === "string") {
    value = parseFloat(value);
  }
  if (isNaN(value)) return "";

  // Convert meters to kilometers (or another unit if specified)
  let convertedValue;
  switch (unit) {
    case "km":
      convertedValue = value; // Convert meters to kilometers
      break;
    case "m":
      convertedValue = value * 1000; // Keep value in meters
      break;
    case "mi":
      convertedValue = value / 1.60934; // Convert meters to miles
      break;
    default:
      convertedValue = value; // Default to kilometers if no matching unit is found
  }

  // Format the converted value with commas and up to 2 decimal places
  return (
    convertedValue.toLocaleString("en-US", {
      minimumFractionDigits: 0,
      maximumFractionDigits: 2,
    }) +
    " " +
    unit
  );
});
